//
// Alerts
// --------------------------------------------------


// Base styles
// -------------------------

// Dismissible alerts
//
// Expand the right padding and account for the close button's positioning.

.alert-dismissable, // The misspelled .alert-dismissable was deprecated in 3.2.0.
.alert-dismissible {
  // Adjust close link position
  .close {
    color: #B3B3B3;
		.opacity(1);
  }
}


// Alternate styles
//
// Generate contextual modifier classes for colorizing the alert.

.alert-success {
  .alert-variant-pl(@alert-success-bg; @alert-success-border; @alert-success-text);
}

.alert-info {
  .alert-variant-pl(@alert-info-bg; @alert-info-border; @alert-info-text);
}

.alert-warning {
  .alert-variant-pl(@alert-warning-bg; @alert-warning-border; @alert-warning-text);
}

.alert-danger {
  .alert-variant-pl(@alert-danger-bg; @alert-danger-border; @alert-danger-text);
}
