@media (max-width: @screen-sm-max) {	
  .pn-container {
		background: #008cbf;
		z-index: 9;
		padding: 10px 0;
		margin-bottom: 15px;	
		
		.sticky-placeholder {
			height: 0 !important;
		}
  }
  .portal-nav {
		position: relative;
		top: 0;
		height: auto !important;
		background: none;
		padding-top: 0;
		border: 0;
		&.pn-nav-open {
			.pn-menu {
				display: block !important;
			}
			.pn-title {
				> h1,
				> h2 {
					.ss-icon {
						.rotate(180deg);
						.transform-origin(50% 40%);
					}
					[class^="fa fa-"] {
						.rotate(180deg);
						.transform-origin(50% 40%);
					}
				}
			}
		}
		+ .sticky-placeholder {
			height: 0;
		}
	}
	.pn-title {
		padding-top: 0;
		> h1,
		> h2 {
			font-size: 25px;
			line-height: 29px;
			margin: 0;
			float: left;
			.ss-icon {
				font-size: 16px;
				line-height: 13px;
				margin-top: -13px;
				display: inline-block;
				padding: 5px 13px;
				cursor: pointer;
				//.transition(all .3s);				
			}
			[class^="fa fa-"] {
				font-size: 16px;
				line-height: 13px;
				margin-top: -13px;
				display: inline-block;
				padding: 5px 13px;
				cursor: pointer;
				//.transition(all .3s);				
			}
		}
	}
	.pn-menu {
		margin-right: 0 !important;
		> ul {
			margin-top: 10px;
		}
	}
	.pn-menu-list {
		> li {
			display: block;
			position: relative;
			border-top: 1px solid #B3EBFF;
			&:first-child {
				border: 0;
			}
			> .pn-menu-item-lv1 {
				> a {
					color: #fff;
					padding-right: 50px;
					margin-left: 0;
					&:before {
						display: none;
					}
				}
				> span {
					display: block;
					position: absolute;							
					right: 0;
					top: 0;
					width: 100%;
					height: 100%;
					cursor: pointer;
					> em {
						position: absolute;
						width: 40px;
						height: 38px;
						top: 0;
						right: 0;
						&:before,
						&:after {
							position: absolute;
							top: 50%;
							right: 7px;
							display: block;
							width: 26px;
							height: 2px;
							margin-top: -1px;
							background-color: #FFF;
							content: "";
						}
						&:after {
							right: 19px;
							width: 2px;
							height: 26px;
							margin-top: -13px;
						}
					}
				}
				&:only-child {
					> span {
						width: 40px; 
						> em {
							-webkit-transform: rotate(-133deg);
							-moz-transform: rotate(-133deg);
							-ms-transform: rotate(-133deg);
							-o-transform: rotate(-133deg);
							transform: rotate(-133deg);
							top: 5px;
							right: -6px;
							position: absolute;
							
							&:before {
								width: 14px;
							}
							
							&:after {
								height: 14px;
							}
						}
					}  
				}
			}
			> .pn-dropdown {
				position: relative;
				border: none;
				margin: 0;
			}
			&:hover {
				> .pn-dropdown {
					display: none;
				}
			}
			&.pn-click-opened {
				.pn-dropdown {
					display: block;
					height: auto;
				}
				> .pn-menu-item-lv1 {
					> span {
						em {
							&:after {
								display: none;
							}
						}
					}
					&:only-child {
						> span {
							> em {
								&:after {
									display: block;
								}
							}
						}
					}
				}
			}
			&.pn-hover-opened {
				 > .pn-menu-item-lv1 {
					> a {
						&:before {
							display: none;
						}
					}
				} 
			}
		}
	}
	.sticky-header-placeholder {
		height: 0px !important;
	}
	.sticky-header-active {
		top: 0 !important;
		background: transparent;
		.pn-menu-list {
			> li {
				.pn-menu-item-lv1 {
					> a {
						color: #fff;
					}
				}
			}
		}
	}
	
	body .pn-container .sticky-header-active .pn-title{
		display: block;
	}
}
@media (max-width: @screen-xs-max) {
	.pn-menu-list {
		> li {
			> .pn-dropdown {
				[class*="col-"] {
					width: 100%;
				}
			}
		}
	}
}
@media (min-width: 568px) and (max-width: @screen-sm-max) {
	.pn-title {
		.pn-roles {
			line-height: 29px;
			bottom: 0;
		}
	}
}
@media (max-width: 567px) {
	.pn-title {
		> h1,
		> h2 {
			float: none;
		}
		.pn-roles {  
			clear: both;
			position: static;
			display: block;
			> .dropdown-toggle {
				position: relative;
				display: block;
				background: #003d53;
				padding: 10px 6px 10px 12px;
				margin-top: 8px!important;
				color: #fff;
				font-size: 12px;
				font-weight: bold;
				line-height: 18px;
				text-transform: uppercase;
				border-radius: 3px;
				&:hover,
				&:focus,
				&:active {
					text-decoration: none;
				}
				.pn-r-dt-icon {
					position: absolute;
					right: 0;
					top: 0;
					bottom: 0;
					width: 62px;
					border-left: 1px solid #002833;
					border-radius: 0 3px 3px 0;
					> em {
						display: none;
					}
					[class^=ss-] {
						font-size: 16px;
						line-height: 24px;
						position: absolute;
						top: 50%;
						left: 50%;
						margin-top: -10px;
						margin-left: -8px;
					}
					> i {
						&[class^="fa fa-"] {
							font-size: 28px;
						}
					}
					> em {
						&[class^="fa fa-"] {
							font-size: 16px;
						}
					}
					[class^="fa fa-"] {	
						font-size: 16px;
						line-height: 24px;
						position: absolute;
						top: 50%;
						left: 50%;
						margin-top: -10px;
						margin-left: -8px;
					}
				}				
			}				
			ul {
				display: none;
				position: absolute;
				right: 0;
				left: 0;
				background: #F7F7F7;
				z-index: 10;
				margin-top: 3px;
				border-radius: 3px;
				.box-shadow(0 0 6px rgba(0,0,0,.45));
				> li {
					display: block;
					margin: 0;
					&:first-child {
						border-radius: 3px 3px 0 0;
					}
					&:last-child {
						border-radius: 0 0 3px 3px;
					}
					> a {
						padding: 5px 12px;
						color: #333;
						display: block;	
						border-radius: 0;				
						&:hover {
							background: #CBEBF7;
						}
						&:active {
							color: #fff;
							background: #00678D;
						}
					}
					&:hover,
					&:active,
					&.active {
						> em {
							display: none;
						}						
					}
					&.active {
						> a {
							color: #fff;
							&,
							&:hover,
							&:active {
								background: #29373D;
							}
						}						
					}
				}				
			}
			&.open {
				.pn-roles-list {
					display: block;
				}
				.pn-r-dt-icon {
					background: #007AA6;
					> i {
						display: none;
					}
					> em {
						display: block; 
					}
				}
			}			
		}
	}
}