// Core variables and mixins
body {
	&.unsticky-body {
		.gn-container {
			> .sticky-header-placeholder {
				height: 87px;
			}
			.global-nav-oz {
				height: 87px;
				.transition(height .3s ease);
			  	position: fixed;
			  	top: 0;
			  	width: 100%;
			}
			+ .pn-container {
				.portal-nav {
					top: 87px;
				}
			}
			&.gn-no-nav {
				> .sticky-header-placeholder {
					height: 54px;
				}
				.global-nav-oz {
					height: 54px;
					.transition(height .3s ease);
				}
				+ .pn-container {
					.portal-nav {
						top: 54px;
					}
				}
			}
		}			
		.pn-container {
			margin-top: 0;
		}
	}
	&.sticky-body {
		.gn-container {
			> .sticky-header-placeholder {
				height: 54px; 
			}
			.global-nav-oz {
				position: fixed;
				width: 100%;
				top: 0;			
				height: 54px;
				.transition(height .3s ease);
				.box-shadow(none);
			}
		}

		.gn-logo {
			> a {
				background: url('../images/stx-staticlogo-drk-nopad-109x26.png');
				background-repeat: no-repeat;
				width: 109px;
				height: 26px;
				line-height: 26px;
			}
			.seagate-portal-logo {
				display: block;
				background: none;
				top: 10px;
				left: 120px;
				text-align: left;
				padding-left: 15px;
				width: auto;
			}
		}
		.pn-container {
			margin-top: -55px;
		}
		.portal-nav {
			.box-shadow(0 2px 4px 0 rgba(0, 0, 0, 0.35));
		}
		.pn-menu-list > li.active .pn-menu-item-lv1 > a:after {
			-webkit-filter: drop-shadow(0 2px 1px rgba(0, 0, 0, 0.35));
			filter: drop-shadow(0 2px 1px rgba(0, 0, 0, 0.35));
		}
	}
}
.gn-logo {
	.seagate-portal-logo {
		display: none;
		position: absolute;
		white-space: nowrap;
		top: 14px;
		left: 140px;
		font-size: 14px;
		line-height: 19px;		
		font-weight: bold;
		color: #333;
	}
}

.pn-container {
	position: relative;
	z-index: 9;
	background: none;	
	margin-bottom: @line-height-computed;
	a {
		color: @link-color; 
		&:hover {
			color: @link-color;
		}
	}
	ul {
		margin: 0;
		padding: 0;
		li {
			list-style: none;
		}
	}	
}
.portal-nav {
	height: auto;
	position: fixed;
	width: 100%;
	top: 0;
	left: 0;
	right: 0;
	background: #007AA6;
	background: #008cbf;
	.transition(top .3s ease);	
}
.pn-title { 
	padding-top: 15px;
	position: relative;
	> h1,
	> h2 {
		line-height: 36px;
		margin-bottom: 10px;
		margin-top: 0;
		color: #fff;
		> a {
			&,
			&:hover {
				color: #fff;
				text-decoration: none;
			}
		}
		.ss-icon {
			display: none;
		}
		[class^="fa fa-"] {
			display: none;
		}
	}
	.pn-roles {
		position: absolute;
		right: 0;
		bottom: 18px;
		> .dropdown-toggle {
			display: none;
		}
		ul {
			> li {
				display: inline-block;
				margin-left: 5px;
				position: relative;
				//border-bottom: 2px solid #8fbccc;
				> a {
					//color: #CBE3EF;
					color: #fff;
					position: relative;
					font-size: 12px;
					text-transform: uppercase;
					font-weight: bold;
					text-decoration: none;
					.transition(color .3s);					
					z-index: 1;
					border-radius: 3px;
					padding: 4px 8px; 
				}
				&:hover {
					> a {
						background: #006589;	
					}					
				}
				&.active {
					> a {						
						cursor: default;
						background: #003d53;
						&:before {
							display: none;
						}
					}
				}
				&:first-child {
					margin-left: 0;
				}
			}			
		}
	}
}	

.pn-menu {
	position: relative;
	font-size: @font-size-small;
	line-height: @line-height-small-computed;
}
.pn-menu-list {	
	> li {
		position: static;
		display: table-cell;
		vertical-align: bottom;
		.pn-menu-item-lv1 {
			> a {
				position: relative;
				padding: 9px 0 12px;
				//color: #CCF1FF;
				color: #fff;
				//text-shadow: 1px 1px 0px rgba(0,0,0,.35);
				display: block;
				text-decoration: none;
				margin-left: 35px;
				font-weight: bold;
				.transition(color .3s);
				&:before {
					content: '';
					display: none;
					position: absolute;
					bottom: -1px;
					left: 50%;
					height: 0;
					width: 0;
					margin-left: -6px;
					border-color: transparent transparent #fff transparent;
					border-style: solid;
					border-width: 0 6px 6px;
					content: "1";
					font-size: 0;
					z-index: 1;
					//background: transparent;
					//transition: width .5s ease, background-color .5s ease, left .5s ease;
					//.transition-properties(width .3s ease, background-color .3s ease, left .3s ease;);
				}
				&:hover {
					color: #99d9f1;
				}
			}
		}
		&.active {
			.pn-menu-item-lv1 {
				> a {
					color: #CCF1FF;
					color: #003d53;
					color: #002533;
					&:after {
						position: absolute;
						bottom: -6px;
						left: 50%;
						width: 0;
						height: 0;
						margin-left: -6px;
						border-color: #008cbf transparent transparent transparent;
						border-style: solid;
						border-width: 6px 6px 0;
						content: "";
						z-index: 0;
						opacity: 1;						
					}
				}
			}
			&.pn-hover-opened {
				.pn-menu-item-lv1 {
					> a {
						&:after {
							opacity: 0;
							.transition(opacity .3s);
						}
					}
				}
			}
		}
		&:first-child {
			.pn-menu-item-lv1 {
				> a {
					margin-left: 0;
				}
			}
		}
		> .pn-dropdown {
			height: 0;
			opacity: 0;
			filter: alpha(opacity=0);
			overflow: hidden;
			position: absolute;
			background: #fff;
			left: 0;
			right: 0;
			//top: 100%;
			//margin-top: 2px;
			.pn-dropdown-menu {
				&.row {
					margin-bottom: 0;
				}
				[class*="col-"] {
					margin-left: 0;

				}				
				.pn-dd-title {
					padding: 10px 0;
					margin: 0 15px;
					border-bottom: 1px solid #e0e0e0;
					p {
						margin-bottom: 0;
						font-size: 14px;
						line-height: 19px;
					}
					h6 {
						font-size: 19px;
						font-weight: 400;
						line-height: 24px;
						text-transform: none;
						margin-bottom: 3px;
					}
				}				
			}
			ul.dm-item-list {
				margin: 7px 15px 15px 15px;				
				> li {
					padding: 5px 0;
					
					> p {
						font-size: 12px;
						line-height: 15px;
						margin-bottom: 4px;
					}
					
					> a + p {
						padding: 4px 0;
						margin: 0;
					}
					
					> p {
						
						&:first-child{
							padding: 4px 0;
							font-size: 14px;
							line-height: 19px;
							margin: 0;
						}
						
					}
					
					> ul {
						line-height: 19px;
						padding: 4px 0 0 16px;
						
						> li {
							list-style: disc;
						}
					}
				}
			}
		}		
		&.pn-hover-opened {
			> .pn-menu-item-lv1 {				
				> a {
					color: #99d9f1;					
					&:before {
						display: block;
						content: "";
					}
				}				
				&:only-child {					
					> a {						
						&:before {
							display: none;
						}
					}
				}
			}
			.pn-dropdown {
				z-index: 2;
				opacity: 1;
				height: auto; 
				filter: alpha(opacity=100);
				.transition(all .1s .1s ease);
			}
		}
		&.pn-click-opened {
			.pn-dropdown {
				z-index: 2;
				opacity: 0;
				height: 0;
				filter: alpha(opacity=0);
			}
		}
	}
}
.pn-nav-open{
  .pn-menu-list {
    > li {   
      &.pn-click-opened {
        
        .pn-dropdown {
            z-index: 2;
            opacity: 1;
            height: auto;
            filter: alpha(opacity=100);
        }
      }
    }
  }
}



.pn-menu-overlay {
	position: absolute;
	width: 100%;
	left: -1px;
	//top: 100%;
	//margin-top: 2px;
	background: #fff;
	height: 0;
	.box-shadow(0 3px 8px rgba(0,0,0,.35));
}

.pn-nav-pull .pn-menu-overlay {
	.transition(height .3s ease);
	border: 1px solid #aaa;
}

body {
	&.unsticky-body {
		.pn-menu {
			margin-right: 0 !important;
		}
	}	
	&.sticky-body {
		.pn-title {
			height: 55px;
		}
		.pn-roles {
			top: 100%;
			font-size: 12px;
			line-height: 19px;
			right: 0;
			position: absolute;
			float: right; 
			&:hover {
				ul {
					display: block;
				}
			}
			
			> .dropdown-toggle {
				display: block;
			  	color: #fff;
  				text-transform: uppercase;
			  	background: #003d53;
			  	border-radius: 3px;
			  	padding: 4px 15px 4px 10px;
			  	height: 25px;
			  	
  				> span {
  					float: left;
  					margin-right: 10px;
  				}
				.pn-r-dt-icon {
					  position: absolute;
					  right: 5px;
					  width: 14px;
					  height: 14px;

					 [class^=ss-] {
					  	position: absolute;
				  	  	color: #00a1dd;
  						vertical-align: top;
					}
					 [class^="fa fa-"] {
					  	position: absolute;
				  	  	color: #00a1dd;
  						vertical-align: top;
  						font-size: 16px;
					}
					> em {
						display: none;
					}
				} 
			}
			ul {
				display: none;
				text-align: right;
  				position: absolute;
			  	padding: 5px;
			  	background: #fff;
			  	border: 1px solid #333;
			  	right: 5px;
			  	&:before,
			  	&:after {
		  		  position: absolute;
				  top: -14px;
				  right: -1px;
				  bottom: 100%;
				  width: 0;
				  height: 0;
				  pointer-events: none;
				  border: solid transparent;
				  content: " ";
			  	}
			  	&:before {
			  		margin-top: -2px;
				  	border-color: transparent;
				  	border-bottom-color: #333333;
				  	border-width: 8px;
			  	}
			  	&:after {
		  		  	right: 0;
				  	border-color: transparent;
				  	border-bottom-color: #ffffff;
				  	border-width: 7px;
			  	}
				> li {
					display: block;
					margin: 0;
					> em {
						left: 0;
						padding: 0;
						border-radius: 0;
						display: none;
						
					}
					> a {
						white-space: nowrap;
						font-weight: normal;
						color: @text-color;
						display: block;
						padding: 4px 6px;
						border-radius: 0;
						&:hover {
							text-decoration: none;
  							background-color: #CBEBF7;
						}
					}
					&.active {
						> em {
							display: block;
						}
						> a {
							color: #fff;
						  	padding: 4px 6px 4px 22px;
							&:hover {
								text-decoration: none;
  								background-color: #003d53; 
							}
							&:before {
								display: inline-block;
								position: absolute;
								left: 4px;
								content: "✓";
								font-family: "SSStandard";
							  	font-style: normal;
							  	font-weight: normal;
							  	text-decoration: none;
							  	text-rendering: optimizeLegibility;
							  	white-space: nowrap;
							  	-moz-font-feature-settings: "liga=1";
							  	-moz-font-feature-settings: "liga";
							  	-ms-font-feature-settings: "liga" 1;
							  	-o-font-feature-settings: "liga";
							  	font-feature-settings: "liga";
							  	-webkit-font-smoothing: antialiased;
							}
						}
					}
				}
			}
		}
	}
}