// Search as you type
.sf-sayt {
	border: 1px solid #333;
	background: #fff;
	position: absolute;
	z-index: 99;
	top: 100%;
	left: -1px;
	right: -1px;	
	margin-top: 2px;
	font-size: @font-size-small;
	line-height: @line-height-small;
}
.sf-sayt-c-body {
	> ul {
		padding: @padding-base-vertical 0;
		margin: 0;
		list-style: none;
		> li {
			padding: 3px @input-padding-base-horizontal;
			cursor: default;
			&:hover {
				background: @link-hover-bg;
			}
			&:active {
				color: #fff;
				background: @link-active-color;
			}
		}
	}
}
.sf-sayt-cat {
	font-size: 0;
	.sf-sayt-container {
		display: inline-block;
		vertical-align: top;
		width: 100%;
		margin-bottom: @padding-base-vertical;
		@media (min-width: @screen-sm-min) {
			width: 50%;
			&.sf-sayt-c-highlight {
				width: 100%;
			}
		}
	}
	.sf-sayt-c-body {
		font-size: @font-size-small;
		padding: @padding-base-vertical @input-padding-base-horizontal 0;
		.sf-sayt-title {
			font-weight: bold;
			margin: 0;
		}
		.sf-sayt-show-more {
			padding: 2px 0;
		}
		> ul {
			padding: (@padding-base-vertical / 2) 0;
			> li {
				padding-left: 0;
				padding-right: 0;
			}
		}
	}
	.sf-sayt-show-all {
		font-size: @font-size-small;
		
	}
	@media (min-width: @screen-md-min) {
		width: 360px;
		&.sf-sayt-thumb {
			width: 440px;
		}
	}
}
.sf-sayt-thumb {
	.sf-sayt-c-left {
		padding: @padding-base-vertical 0 0 @input-padding-base-horizontal;
		float: left;
		img {
			max-width: 40px;			
		}
		> a {
			display: block;
		}
	}
	.sf-sayt-c-body {
		overflow: hidden;
	}
}
.sf-sayt-c-highlight {
	position: relative;
	margin-bottom: 0;
	padding-bottom: 10px;
	.sf-sayt-c-left {
		text-align: center;
		img {
			max-width: 70px;
			margin-bottom: 5px;
		}
	}	
	&:after {
		position: absolute;
		bottom: 0px;
		left: @input-padding-base-horizontal;
		right: @input-padding-base-horizontal;
		content: "";
		height: 1px;
		background: #e3e3e3;
		display: block;
	}
}

.sf-sayt-show-all {
	text-align: center;
	border-top: 1px solid #e3e3e3;
	padding: @padding-base-vertical @input-padding-base-horizontal;
}

.sf-xs {
	.sf-sayt {
		.sf-sayt-c-body {
			font-size: @font-size-xs;
			> ul {
				padding: @input-padding-xs-vertical 0;
				> li {
					padding: 2px @input-padding-xs-horizontal;
				}
			}			
		}
		.sf-sayt-show-all {
			font-size: @font-size-xs;
			padding: @input-padding-xs-vertical @input-padding-xs-horizontal;
		}
	}
	.sf-sayt-cat {
		.sf-sayt-container {
			margin-bottom: @input-padding-xs-vertical;
		}
		.sf-sayt-c-body {
			font-size: @font-size-xs;
			padding: @input-padding-xs-vertical @input-padding-xs-horizontal 0;
			> ul {
				padding: (@input-padding-xs-vertical / 2) 0;
				> li {
					padding-left: 0;
					padding-right: 0;
				}
			}			
		}
	}
	.sf-sayt-c-highlight {
		padding-bottom: (@input-padding-xs-vertical * 2);
		&:after {
			left: @input-padding-xs-horizontal;
			right: @input-padding-xs-horizontal;
		}
	}
	.sf-sayt-thumb {
		.sf-sayt-c-left {
			padding: @input-padding-xs-vertical 0 0 @input-padding-xs-horizontal;
		}		
	}
}

.sf-sm {
	.sf-sayt {
		.sf-sayt-c-body {
			> ul {
				padding: @padding-small-vertical 0;
				> li {
					padding: 2px @input-padding-small-horizontal;
				}
			}
		}
		.sf-sayt-show-all {
			padding: @padding-small-vertical @input-padding-small-horizontal;
		}
	}
	.sf-sayt-cat {
		.sf-sayt-container {
			margin-bottom: @padding-small-vertical;
		}
		.sf-sayt-c-body {
			padding: @padding-small-vertical @input-padding-small-horizontal 0;				
			> ul {
				padding: (@padding-small-vertical / 2) 0;
				> li {
					padding-left: 0;
					padding-right: 0;
				}
			}			
		}
	}
	.sf-sayt-c-highlight {
		padding-bottom: @padding-small-vertical;
		&:after {
			left: @input-padding-small-horizontal;
			right: @input-padding-small-horizontal;
		}
	}
	.sf-sayt-thumb {
		.sf-sayt-c-left {
			padding: @padding-small-vertical 0 0 @input-padding-small-horizontal;
		}		
	}
}

.sf-lg {
	.sf-sayt {
		.sf-sayt-c-body {
			font-size: @font-size-base;
			line-height: @line-height-large;
			> ul {
				padding: @padding-large-vertical 0;
				> li {
					padding: 2px @input-padding-large-horizontal;
				}
			}
		}
		.sf-sayt-show-all {
			padding: @padding-large-vertical @input-padding-large-horizontal;
		}
	}
	.sf-sayt-cat {
		.sf-sayt-c-body {
			padding: @padding-large-vertical @input-padding-large-horizontal 0;			
			> ul {
				padding: (@padding-large-vertical / 2) 0;
				> li {
					padding-left: 0;
					padding-right: 0;
				}
			}			
		}
		@media (min-width: @screen-md-min) {
			&.sf-sayt-thumb {
				width: 480px;
			}
		}
	}
	.sf-sayt-thumb {
		.sf-sayt-c-left {
			padding: @padding-large-vertical 0 0 @input-padding-large-horizontal;
		}		
	}
}
