//
// Scaffolding
// --------------------------------------------------



// Body reset

body {
  font-size: @font-size-small;
	line-height: @line-height-small;
	@media (min-width: @screen-md-min) {
		font-size: @font-size-base;
		line-height: @line-height-base;
  }
}



// Links

a {
  &:hover,
  &:focus {
		color: @link-color;
  }
	
	&:active,
	&.active {
    color: @link-active-color;
	}
}
