.gn-user {
	.horizontal-sub-navigation {
			margin: 0 12px;
	}
	> .gn-icon-container {
		> span {
			&.gn-ic-text {
				width: auto;
			}
		}
	}
	&:hover {
	}
	@media (min-width: @screen-md-min) {
		.horizontal-sub-navigation {
			background: transparent;
			& + .tab-content {
				padding: 12px;
			}
		}
		.hsn-to-dropdown {
			background: transparent;
			> .hsn-to-drop {
				width: 50%;
				text-align: left;
				.hsn-to-item {
					padding: 0;
					> a {
						padding-left: 13px;
						padding-right: 13px;
						text-align: center;
					}
				}
			}
		}
	}
	.gn-user-login-form {
		min-width: 300px;
		.hr-full-width {
			margin: 15px -12px;
		}
		.tab-pane-footer {
			text-align: center;
			font-size: @font-size-xxs;
			line-height: @line-height-xxs-computed;
		}
	}
}