.gn-tools {
  	margin-top: 15px;
	> a,
	.gn-icon {
		display: inline-block;
		margin-top: 3px;
		margin-left: 14px;
		font-size: 13px;
		line-height: 23px;
	}
	> a {
		display: none;
		color: @text-color;
		&:hover {
			color: @link-color;
			text-decoration: underline;
		}
		+ img {
			float: left;
		}
		@media (min-width: @screen-sm-min) {
			display: inline-block;
		}
	}
	.gn-icon {
		position: relative;
		&:hover {
			.gn-icon-drop-container {
  			display: block;
			}
		}
		i {
			&[class^="ss-"] {
				width: 12px;
				height: 20px;
				padding: 5px 0 0;
				font-size: 12px;
				line-height: 20px;
				color: @link-color;
			}
			&[class^="fa fa-"] {
				width: 12px;
				height: 20px;
				line-height: 20px;
				color: #00a1dd;
				font-size: 20px;
		   }
		}
		.gn-icon-container {
			> span {
				display: none;
			}
		}
		.gn-icon-drop-container {
			margin-top: -3px;
			position: absolute;
			right: -2px;
			z-index: 101;
			display: none; 
			min-width: 142px;
			padding: 8px 0 0;
	  	
			.gn-icon-drop {
				background: @white-base;
				border: 1px solid #333333;
				color: @text-color;
				padding: 10px 0 0;
				text-align: right;
				&:before,
				&:after {
					bottom: 100%;
					right: 0;
					border: solid transparent;
					content: " ";
					height: 0;
					width: 0;
					position: absolute;
					pointer-events: none;
					top: -5px;
				}
				&:after {
					right: 1px;
					border-color: transparent;
					border-bottom-color: @white-base;
					border-width: 7px;
					margin-left: -8px;
				}
				&:before {
					border-color: transparent;
					border-bottom-color: #333333;
					border-width: 8px;
					margin-left: -16px;
					margin-top: -2px;
				}
				p,
				a	{
					display: block;
					font-size: 12px;
					line-height: 15px;			
					padding: 0 10px;
					margin: 0 0 8px;
					white-space: nowrap;
					vertical-align: top;
				}
				a {
					&,
					&:hover {
						color: @link-color;
					}
					&:hover,
					&:active,
					&:focus {
						text-decoration: underline;
					}
				}
				hr {
					position: static;
					width: auto;			
					height: auto;
					margin: 0 0 8px;
					border: 0;
					border-top: 1px solid #e6e6e6;
				}
				ul {
					list-style: none;
					margin: 0;
					padding: 0;
					> li {
						list-style: none;
						&.gn-tools-dup {
							display: none;
						}
					}
				}
			}
		}
	}		
}

/*Responsive*/
@media (max-width:@screen-sm-max) {
	.gn-tools {
		margin-top: 0;
		> a {
			height: 23px;
			line-height: 23px;
			padding: 12px 14px 14px;
			margin: 0;
			vertical-align: top;
		}
		> .gn-icon {
			margin: 0;
			position: static;
			.gn-icon-container {
				height: 23px;
				width: 16px;
				position: relative;
				.box-sizing(content-box);
				> span {
					display: block;  
					padding: 15px 14px;
					position: absolute;
					left: 0;
					top: 0;
					bottom:0;
					right: 0;
					&[class^="fa fa-"] {
						font-size: 14px;
	    				line-height: 19px;
	    			}
				}
			}
			&:hover {
				.gn-icon-drop-container {
					display: none;
				}
			}
			.gn-icon-drop-container {
				margin-top: 0;
				.gn-icon-drop {
					text-align: left;
					border-width: 1px 0 0 0;
					padding: 10px 0;
					.box-sizing(border-box);
					[class^="ss-"] {
						width: 14px;
						height: 14px;
						padding: 4px 14px;
						font-size: 14px;
						line-height: 19px;
						color: #999;
						cursor: pointer;
						.box-sizing(content-box);
					}
					[class^="fa fa-"] {
						width: 14px;
						height: 14px;
						padding: 4px 14px;
						font-size: 18px;
						line-height: 18px;
						color: #999;
						cursor: pointer;
						.box-sizing(content-box);
					}
					p,
					a {
						display: block;
						padding: 0 12px;
						margin: 0 0 10px;
						font-size: 16px;
						line-height: 23px;
						white-space: normal;
					}
					p {
						margin-right: 50px;
					}
					&:before,
					&:after {
						right: 101px;
						top: -13px;
					}
					&:before {
						right: 100px;
					}
				}
			}
			
		}
		&.gn-tools-opened {
			.gn-icon {
				&:hover {
					.gn-tools-drop-container {
						display: block;
					}
				}
			}
			.gn-tools-drop-container {
				display: block; 
				right: 0;
				left: 0;
				padding: 0;
			}
			.gn-icon-container {
				[class^="ss-"] {
					color: @link-color;
				}
				[class^="fa fa-"] {
					color: @link-color;
				}
			}
		}
	}
	.gn-lite-3 {
		.gn-tools {
			> .gn-icon {				
				.gn-icon-drop-container {
					.gn-icon-drop {						
						&:before,
						&:after {
							right: 24px;
						}
						&:before {
							right: 23px;
						}
					}
				}				
			}
		}		
	}
	.gn-lite-4 {
		.gn-tools {
			margin-right: 40px;
			> .gn-icon {				
				.gn-icon-drop-container {
					.gn-icon-drop {						
						&:before,
						&:after {
							right: 64px;
						}
						&:before {
							right: 63px;
						}
					}
				}				
			}
		}		
	}
}

@media (min-width: @screen-md-min) {
	.gn-tools {
		> .gn-icon {
			.gn-tools-drop-container {
				.gn-icon-drop {
					min-height: 0!important;
				}
			}
		}
	}
	
}
