//
// Forms
// --------------------------------------------------


// Normalize non-controls
//
// Restyle and baseline non-control form elements.

label {
  margin-bottom: 8px;
	.hint-text {
		font-weight: normal;
		margin-left: 5px;
	}
	.ht-optional {
		color: #999;
	}
	.ht-required {
		color: @state-danger-text;
	}
}

.form-label {
	font-weight: bold;
	margin-bottom: 6px;
}

// Normalize form controls
//
// While most of our form styles require extra classes, some basic normalization
// is required to ensure optimum display with or without those classes to better
// address browser inconsistencies.


// Position radios and checkboxes better
input[type="radio"],
input[type="checkbox"] {
  margin: 5px 0 0;
}



// Common form controls
//
// Shared size and type resets for form controls. Apply `.form-control` to any
// of the following form controls:
//
// select
// textarea
// input[type="text"]
// input[type="password"]
// input[type="datetime"]
// input[type="datetime-local"]
// input[type="date"]
// input[type="month"]
// input[type="time"]
// input[type="week"]
// input[type="number"]
// input[type="email"]
// input[type="url"]
// input[type="search"]
// input[type="tel"]
// input[type="color"]

.form-control {
  padding: @padding-base-vertical @input-padding-base-horizontal (@padding-base-vertical - 1);
  font-size: @font-size-small;
  line-height: @input-line-height-base;
  .transition(~"border-color linear .2s, box-shadow linear .2s");

  // Customize the `:focus` state to imitate native WebKit styles.
  .form-control-focus-pl();
}


// Checkboxes and radios
//
// Indent the labels to position radios/checkboxes as hanging controls.

.radio,
.checkbox {
  margin-top: 0;
}


// Form control sizing
//
// Build on `.form-control` with modifier classes to decrease or increase the
// height and font-size of form controls.
//
// The `.form-group-* form-control` variations are sadly duplicated to avoid the
// issue documented in https://github.com/twbs/bootstrap/issues/15074.

.input-xs {
  .input-size-pl(@input-height-xs; @input-padding-xs-vertical; (@input-padding-xs-vertical - 1); @input-padding-xs-horizontal; @font-size-xs; @input-line-height-xs; @border-radius-base);
}
.form-group-xs {
  .form-control {
		.input-size-pl(@input-height-xs; @input-padding-xs-vertical; (@input-padding-xs-vertical - 1); @input-padding-xs-horizontal; @font-size-xs; @input-line-height-xs; @border-radius-base);
  }
  .form-control-static {
    height: @input-height-xs;
    padding: @input-padding-xs-vertical @input-padding-xs-horizontal (@input-padding-xs-vertical - 1);
    font-size: @font-size-xs;
    line-height: @input-line-height-xs;
    min-height: (@input-line-height-xs + @font-size-xs);
  }
}

.input-sm {
  .input-size-pl(@input-height-small; @padding-small-vertical; (@padding-small-vertical - 3); @input-padding-small-horizontal; @font-size-small; @input-line-height-small; @border-radius-base);
}
.form-group-sm {
  .form-control {
		.input-size-pl(@input-height-small; @padding-small-vertical; (@padding-small-vertical - 3); @input-padding-small-horizontal; @font-size-small; @input-line-height-small; @border-radius-base);
  }
  .form-control-static {
    height: @input-height-small;
    padding: @padding-small-vertical @input-padding-small-horizontal (@padding-small-vertical - 3);
    font-size: @font-size-small;
    line-height: @input-line-height-small;
    min-height: (@input-line-height-small + @font-size-small);
  }
}

.input-lg {
  .input-size-pl(@input-height-large; @padding-large-vertical; (@padding-large-vertical - 2); @input-padding-large-horizontal; @font-size-base; @input-line-height-large; @border-radius-base);
}
.form-group-lg {
  .form-control {
    .input-size-pl(@input-height-large; @padding-large-vertical; (@padding-large-vertical - 2); @input-padding-large-horizontal; @font-size-base; @input-line-height-large; @border-radius-base);
  }
  .form-control-static {
    height: @input-height-large;
    padding: @padding-large-vertical @input-padding-large-horizontal (@padding-large-vertical - 2);
    font-size: @font-size-base;
    line-height: @input-line-height-large;
    min-height: (@input-line-height-large + @font-size-base);
  }
}


// Form control feedback states
//
// Apply contextual and semantic states to individual form controls.


// Feedback states
.has-success {
  .form-control-validation-pl(@state-success-text; @state-success-text; @state-success-bg);
}
.has-warning {
  .form-control-validation-pl(@state-warning-text; @state-warning-text; @state-warning-bg);
}
.has-error {
  .form-control-validation-pl(@state-danger-text; @state-danger-text; @state-danger-bg);
}

// Checkboxes and radios with custom styles
//
// Made purely in CSS, target the elements based on its checked status.
// Works in IE9+, Firefox 3.5+, Safari 1.3+, Opera 6+, Chrome anything. In browsers where it doesn't "work", it gracefully degrades back to regular checkboxes / radio buttons.

.checkbox-custom,
.radio-custom {
	display: none;
	&.input-lg {
		+ label {
			padding-left: (@custom-check-radio-size-large + 7);
			font-size: @font-size-base;
			> span {
				top: floor(((@line-height-computed - @custom-check-radio-size-large)/2));
				width: @custom-check-radio-size-large;
				height: @custom-check-radio-size-large;
				font-size: 14px;
				line-height: @custom-check-radio-size-large;
			} 
		}
	}	
	+ label {
		position: relative;
		padding-left: (@custom-check-radio-size-normal + 7);
		font-size: @font-size-small;
		line-height: @line-height-computed;
		> span {
			position: absolute;
			top: floor(((@line-height-computed - @custom-check-radio-size-normal)/2));
			left: 0;
			width: @custom-check-radio-size-normal;
			height: @custom-check-radio-size-normal;
			border: 1px solid #333;
			background-color: #fff;
			> i {
				display: none;
			}
		}
		&:hover,
		&:active {
			> span {
				background-color: @custom-check-radio-hover-bg;
			}
		}
		&:active {
			> span {
				> i {
					color: @blue-darker-more;					
				}
			}
		}
	}
	&:checked + label {
		> span {
			color: @blue-base;
			> i {
				display: block;
			}
		}				
	}
	&[disabled] {
		+ label {
			&,
			&:hover,
			&:active {
				> span {
					background: @custom-check-radio-disabled-bg;
					border-color: #999;
				}
			}
		}
		&:checked + label {
			> span {
				> i {
					color: @blue-lighter;
				}
			}
		}
	}
}


.radio-custom {
	&.input-lg {
		+ label {
			> span {
				width: @custom-check-radio-size-large;
				height: @custom-check-radio-size-large;
				font-size: 11px;
				line-height: @custom-check-radio-size-large;
				> i {
					width: (@custom-check-radio-size-large / 2);
					height: (@custom-check-radio-size-large / 2);
					margin: -4px 0 0 -4px;
				}
			} 
		}
	}
	+ label {
		> span {
			&,
			> i {
				border-radius: 50%;
			}
			> i {
				position: absolute;
				top: 50%;
				left: 50%;
				width: 6px;
				height: 6px;
				margin: -3px 0 0 -3px;
				background-color: transparent;
			}
		}
	}
	&:checked + label {
		&:active {
			> span {
				> i {
					background-color: @blue-darker-more;
				}
			}
		}
		> span {
			> i {
				background-color: @blue-base;
			}
		}
	}
	&[disabled] {
		&:checked + label {
			&,
			&:active {
				> span {
					> i {
						background-color: @blue-lighter;
					}
				}
			}
		}
	}
}

.checkbox-custom {
	+ label {
		> span {			
			font-size: 12px;
			line-height: 15px;
			text-align: center;
		}
	}
	&.checkbox-indet,
	&:indeterminate {
		+ label {
			> span {
				> i {
					display: none;
				}
			}
		}
		&:checked {
			+ label {
				> span {
					&:after {
						content: "";
						position: absolute;
						width: 8px;
						height: 2px;
						background-color: @link-color;
						left: 50%;
						top: 50%;
						margin-top: -1px;
						margin-left: -4px;
					}
				}
			}
		}
		&:active {
			+ label {
				> span {
					&:after {
						background-color: @blue-darker-more;
					}
				}
			}
		}
		&[disabled] {
			&:checked + label {
				&,
				&:active {
					> span {
						&:after {
							background-color: @blue-lighter;
						}
					}
				}
			}
		}
		&.input-lg {
			&:checked {
				+ label {
					> span {
						&:after {
							width: 10px;
							margin-left: -5px;
						}
					}
				}
			}
		}
	}
}
.form-inline {
	.checkbox-custom,
	.radio-custom {
		+ label {
			padding-left: (@custom-check-radio-size-normal + 7);
			font-weight: normal;
			margin-bottom: 0;
		}
		&.input-lg {
			+ label {
				padding-left: (@custom-check-radio-size-large + 7);
			}
		}
	}
}

// Inline forms
//
// Make forms appear inline(-block) by adding the `.form-inline` class. Inline
// forms begin stacked on extra small (mobile) devices and then go inline when
// viewports reach <768px.
//
// Requires wrapping inputs and labels with `.form-group` for proper display of
// default HTML form controls and our custom form controls (e.g., input groups).
//
// Heads up! This is mixin-ed into `.navbar-form` in navbars.less.

.form-inline {

  // Kick in the inline
  @media (min-width: @screen-sm-min) {
    // Inline-block all the things for "inline"
    .form-group {      
			margin-right: 8px;
    }
  }
}


// Horizontal forms
//
// Horizontal forms are built on grid classes and allow you to create forms with
// labels on the left and inputs on the right.

.form-horizontal {
  @media (min-width: @screen-sm-min) {
		&.form-horizontal-left {
			.control-label {
				text-align: left;
			}
		}
  }

  // Validation states
  //
  // Reposition the icon because it's now within a grid column and columns have
  // `position: relative;` on them. Also accounts for the grid gutter padding.
  .has-feedback .form-control-feedback {
    right: (@grid-gutter-width / 2);
  }

  // Form group sizes
  //
  // Quick utility class for applying `.input-lg` and `.input-sm` styles to the
  // inputs and labels within a `.form-group`.
  .form-group-lg {
    @media (min-width: @screen-sm-min) {
      .control-label {
        padding-top: ((@padding-large-vertical * @line-height-large) + 1);
      }
    }
  }
  .form-group-sm {
    @media (min-width: @screen-sm-min) {
      .control-label {
        padding-top: (@padding-small-vertical + 1);
      }
    }
  }
}


// Phone Number
.form-phone-number {
	font-size: 0;
	[class^="fpn-code-"] {
		position: relative;
		display: inline-block;		
		margin-bottom: @form-group-margin-bottom;
		&:first-child {
			margin-left: 0;
		}
	}
	.fpn-code-country {
		width: 50%;
		padding: 0 15px;
		&:before,
		&:after {
			position: absolute;
			left: 0;
			top: 50%;
			font-size: @font-size-base;
			font-weight: bold;
			line-height: @line-height-computed;
			margin-top: -13px;
			content: "(";
			width: 15px;
			text-align: center;
		}
		&:after {
			left: auto;
			right: 0;
			content: ")";
		}
		input {
			text-align: center;
		}
		@media (min-width: @screen-sm-min) {
			width: 100px;
		}
	}
	.fpn-code-area {
		width: 50%;
		padding: 0 15px 0 0;
		&:after {
			position: absolute;
			right: 0;
			top: 50%;
			font-size: @font-size-base;
			font-weight: bold;
			line-height: @line-height-computed;
			margin-top: -13px;
			content: "–";
			width: 15px;
			text-align: center;
		}
		@media (min-width: @screen-sm-min) {
			width: 100px;
		}
	}
	.fpn-code-sub {
		width: 100%;
		@media (min-width: @screen-sm-min) {
			width: 170px;
		}
	}
}