
// Left/right controls for nav
// ---------------------------
.carousel {
	margin-bottom: @line-height-computed;
	z-index: 1;
	&:hover {
		.carousel-control {
			 &.left,
				&.right {
					&.hidden {
						display: block!important;
					}
				}
		}
	}
}
.carousel-control {
	width: 10%;
  text-shadow: none;

  // Set gradients for backgrounds
  &.left,
	&.right {
    background-image: none;
		.reset-filter();
		&.hidden {
			display: none!important;
		}
  } 

  // Hover/focus state
	&:hover,
  &:focus {
		color: @carousel-control-color-hover;
    .opacity(1);
  }
	&:active {
		color: @carousel-control-color-active;
  }

  // Toggles
  .ss-icon {
		position: absolute;
		display: inline-block;
		z-index: 5;
		top: 50%;
		width: @carousel-control-font-size;
		height: @carousel-control-font-size;
		margin-top: (-(@carousel-control-font-size / 2));
  }
  [class^="fa fa-"] {
		position: absolute;
		display: inline-block;
		z-index: 5;
		top: 50%;
		width: @carousel-control-font-size;
		height: @carousel-control-font-size;
		margin-top: (-(@carousel-control-font-size / 2));
  }
  &.left .ss-icon {
		left: 50%;
    margin-left: (-(@carousel-control-font-size / 2));
  }
  &.right .ss-icon {
		right: 50%;
    margin-right: (-(@carousel-control-font-size / 2));
  }
   &.left [class^="fa fa-"] {
		left: 50%;
    margin-left: (-(@carousel-control-font-size / 2));
  }
  &.right [class^="fa fa-"] {
		right: 50%;
    margin-right: (-(@carousel-control-font-size / 2));
  }
}


// Optional indicator pips
//
// Add an unordered list with the following class and add a list item for each
// slide your carousel holds.

.carousel-indicators {
  margin-bottom: 0;

  li {
    margin: 1px 5px;
		background-color: @carousel-indicator-bg-color;
  }
  .active {
    margin: 1px 5px;
    width: 10px;
    height: 10px;
    background-color: @carousel-indicator-border-color;
		border-color: @carousel-indicator-bg-color;
  }
}

// Optional captions
// -----------------------------
// Hidden by default for smaller viewports
.carousel-caption {
  text-shadow: none;
	&.text-left {
		text-align: left;
	}
	&.text-right {
		text-align: right;
	}
	&.caption-left {
		width: 50%;
		top: 25%;
		text-align: left;
		.caption-title {
			margin-top: 0;
		}
		.btn {
			text-transform: uppercase;
		}
	}
}


// Scale up controls for tablets and up
@media screen and (min-width: @screen-sm-min) {
	.carousel-caption {
    left: 15%;
    right: 15%;
    padding-bottom: 20px;
  }
  // Move up the indicators
  .carousel-indicators {
    bottom: 10px;
  }
}

@media (max-width: 480px) {
	.carousel-caption {
		&.caption-left {
			top: 0;
			width: auto;
			p {
				display: none;
			}
		}
	}
}
