// Panels

.panel-variant-pl(@background; @heading-bg-color; @border; @table-striped-bg; @table-hover-bg) {
	background-color: @background;
  & > .panel-heading {
		color: @text-color;
    background-color: @heading-bg-color;
    border-color: @border;

    + .panel-collapse > .panel-body {
      border-top-color: @border;
    }
    .badge {
      color: @heading-bg-color;
    }
  }
  & > .panel-footer {
    + .panel-collapse > .panel-body {
      border-bottom-color: @border;
    }
  }
	& > .panel-body {
		.table-striped,
		.table-hybrid {
			> tbody {
				> tr {
					&:nth-of-type(even) {
						background-color: @table-striped-bg;
					}
					&:nth-of-type(odd) {
						background-color: transparent;
					}
					&:hover {
						background-color: @table-hover-bg;
					}
				}
			}
		}
		.table {
			> tbody {
				> tr {
					&:hover {
						background-color: @table-hover-bg;
					}
				}
			}
		}
	}
}
