//
// Panels
// --------------------------------------------------


// Base class
.panel {
  margin-bottom: @line-height-computed;
  background-color: @panel-bg;
	border: 0;
  border-radius: @panel-border-radius;
  .box-shadow(0 1px 3px rgba(0,0,0,.35));
	font-size: @panel-font-size;
	line-height: @panel-line-height;  
}

// Optional heading
.panel-heading {
  padding: @panel-heading-padding;
  border-bottom: 1px solid transparent;
  .border-top-radius(@panel-border-radius);
	font-weight: bold;
  > .dropdown .dropdown-toggle {
    color: inherit;
  }
}

// Within heading, strip any `h*` tag of its default margins for spacing.
.panel-title {
  font-size: @panel-font-size;
	line-height: @panel-line-height;
	font-weight: bold;
}

.panel-body {
	&.pb-full-width {
			padding: 0;
	}
	.list-block-group {
		margin: 0;
		padding: 0;
		list-style: none;
		> li {
			> a {
				display: block;
				padding: 10px;
			}
		}
	}
}

// Optional footer (stays gray in every modifier class)
.panel-footer {
  padding: @panel-footer-padding;
  background-color: @panel-footer-bg;
  border-top: 1px solid @panel-inner-border;
  .border-bottom-radius((@panel-border-radius - 1));
}

// List groups in panels
//
// By default, space out list group content from panel headings to account for
// any kind of custom content between the two.

.panel {
  > .list-group,
  > .panel-collapse > .list-group {
    margin-bottom: 0;

    .list-group-item {
      border-width: 1px 0;
      border-radius: 0;
    }

    // Add border top radius for first one
    &:first-child {
      .list-group-item:first-child {
        border-top: 0;
        .border-top-radius((@panel-border-radius - 1));
      }
    }
    // Add border bottom radius for last one
    &:last-child {
      .list-group-item:last-child {
        border-bottom: 0;
        .border-bottom-radius((@panel-border-radius - 1));
      }
    }
  }
}

// Collapsable panels (aka, accordion)
//
// Wrap a series of panels in `.panel-group` to turn them into an accordion with
// the help of our collapse JavaScript plugin.

.panel-group {
  margin-bottom: @line-height-computed;
  // Tighten up margin so it's only between panels
  .panel {
    margin-bottom: 0;
    border-radius: @panel-border-radius;

    + .panel {
      margin-top: 5px;
    }
  }

  .panel-heading {
    border-bottom: 0;

    + .panel-collapse > .panel-body,
    + .panel-collapse > .list-group {
      border-top: 1px solid @panel-inner-border;
    }
  }

  .panel-footer {
    border-top: 0;
    + .panel-collapse .panel-body {
      border-bottom: 1px solid @panel-inner-border;
    }
  }	
}

// Contextual variations
.panel-default {
  .panel-variant-pl(@panel-default-background; @panel-default-heading-bg; @panel-default-border; @panel-default-table-striped-bg; @panel-default-table-hover-bg);
}
.panel-blue,
.panel-primary {
  .panel-variant-pl(@panel-primary-background; @panel-primary-heading-bg; @panel-primary-border; @panel-primary-table-striped-bg; @panel-primary-table-hover-bg);
}
.panel-green,
.panel-success {
  .panel-variant-pl(@panel-success-background; @panel-success-heading-bg; @panel-success-border; @panel-success-table-striped-bg; @panel-success-table-hover-bg);
}
.panel-orange,
.panel-warning {
  .panel-variant-pl(@panel-warning-background; @panel-warning-heading-bg; @panel-warning-border; @panel-warning-table-striped-bg; @panel-warning-table-hover-bg);
}
.panel-purple {
  .panel-variant-pl(@panel-purple-background; @panel-purple-heading-bg; @panel-purple-border; @panel-purple-table-striped-bg; @panel-purple-table-hover-bg);
}
.panel-dark-gray {
  .panel-variant-pl(@panel-dark-gray-background; @panel-dark-gray-heading-bg; @panel-dark-gray-border; @panel-dark-gray-table-striped-bg; @panel-dark-gray-table-hover-bg);
}

.panel {
	.list-content {
		padding: 0;
		margin: 0;
		> li {
			list-style: none;
			padding: 5px 0;
		}
		&.customized {
			> li {
				padding: 5px 0 5px 13px;
  				text-indent: -13px;
				&:before {
					content: "";
					display: inline-block;
					border-left: 6px solid #333333;
					border-top: 5px solid transparent;
					border-bottom: 5px solid transparent;
					margin-right: 5px;
				}
			}
		}
	}
	&.panel-blue,
	&.panel-primary {
		.list-content {
			&.customized {
				> li {
					&:before {
						border-left-color: #00678D;
					}
				}
			}
		}
	}
	&.panel-green,
	&.panel-success {
		.list-content {
			&.customized {
				> li {
					&:before {
						border-left-color: #78A523;
					}
				}
			}
		}
	}
	&.panel-orange,
	&.panel-warning {
		.list-content {
			&.customized {
				> li {
					&:before {
						border-left-color: #FAB239;
					}
				}
			}
		}
	}
	&.panel-purple {
		.list-content {
			&.customized {
				> li {
					&:before {
						border-left-color: #2F1980;
					}
				}
			}
		}
	}
}

.panel-group {
	&.panel-accordion {
		> .panel {
			.box-shadow(none);
			margin-top: 0;
			margin-bottom: 1px;
			border-radius: 0;
			> .panel-heading {
				padding: 0;
				border-radius: 0;
				+ .panel-collapse > .panel-body,
				+ .panel-collapse > .list-group {
					border-top: 0;
				}
				a {
					&[data-toggle = "collapse"] {
						display: block;
						position: relative;
						padding: 10px 50px 10px 10px;
						&:hover,
						&:focus,
						&:active {
							text-decoration: none;
							color: @text-color;
						}
						> span {
							position: absolute;
							top: 0;
							bottom: 0;
							right: 0;
							width: 40px;
							background: #333;
							&:before,
							&:after {
									position: absolute;
								top: 50%;
								left: 7px;
								display: block;
								width: 26px;
								height: 2px;
								margin-top: -1px;
								background-color: #ffffff;
								content: "";
							}
						}
						&[aria-expanded="true"] {
						  > span {
						  	background: @link-color;
								&:after {
						  		display: none;
							  }
							}
						}
						&[aria-expanded="false"] {
							> span {
								&:after {
									left: 19px;
									width: 2px;
									height: 26px;
									margin-top: -13px;
								}
							}
						}
					}
				}
			}
		}
	}
}



.panel-toggle {
	.panel-heading {
		position: relative;
	}
	.panel-title {
		padding-right: 40px;
	}
	.panel-toggle-icon {
		position: absolute;
		right: 0;
		top: 0;
		bottom: 0;
		width: 30px;
		&:hover {
			text-decoration: none;
		}
		[class^="ss-"] {
			display: none;
		  	color: #ccc;
			top: 50%;
  			position: absolute;
  			margin-top: -9px;
  			font-size: 11px;
  			left: 50%;
  			margin-left: -6px;
		}
		[class^="fa fa-"] {
			display: none;
		  	color: #ccc;
			top: 50%;
  			position: absolute;
  			margin-top: -5px;
  			font-size: 11px;
  			left: 50%;
  			margin-left: -6px;
		}
		&[aria-expanded="true"] {
			.plus {
				display: none;
			}
			.hyphen {
				display: block;
			}
		}
		&[aria-expanded="false"] {
			.plus {
				display: block;
			}
			.hyphen {
				display: none;
			}
		}
	}
}

@media(min-width:@screen-sm-min) {
	.panel-toggle {
		.panel-collapse {
			height: auto!important;
			display: block!important;
		}
	}
}

.expand-collapse {
	display: inline-block;
	&[data-type="single"] {
		&.collapse-all {
			&:before {
				content:"Collapse All";
				display: inline-block;
			}
		}
		&.expand-all {
			&:before { 
				content:"Expand All";
				display: inline-block; 
			}
		}
	}
}
