.tables-and-grids {
	position: relative;
	.table-responsive {
		clear: both;
	}
	.tg-selectable-rows {
		&.selected-one,
		&.selected-all {
			.tg-menu {
				select {

				}
			}
			.tg-button {
				> button {
					
				}
			}
		}
		&.selected-one {
			table {
				thead {
					> tr {
						> th {
							> .selectState {
								> label {
									> span {
										&.all {
											display: none;
										}
										&.any {
											display: block;
										}
									}
								}

							}
						}
					}
				}
			}
		}
		&.selected-all {
			table {
				thead {
					> tr {
						> th {
							> .selectState {
								> label {
									> span {
										&.all {
											display: block;
										}
										&.any {
											display: none;
										}
									}
								}

							}
						}
					}
				}
			}
		}
		.tg-menu {
			float: right;
			margin-bottom: 10px;
			select {

			}
		}
		.tg-button {
			float: right;
			> button {
				
			}
		}
		table {
			.selectState {
				min-width: 20px;
				margin-bottom: 0;
				> label {
					display: block;
					min-height: 18px;
					padding-left: 0;
				}
			}
			thead {
				> tr {
					> th {
						> .selectState {
							> label {
								> span {
									&.all {
										display: block;
									}
									&.any {
										display: none;
									}
								}
							}
						}
					}
				}
			}
			tbody {
				> tr {
					> td {
						.tg-sr-options {
							float: right;
							min-width: 100px;
							.tg-sr-display {
								padding: 0 3px;
								text-align: right;
								.tg-sr-display-text {
									color: @text-color;
									&:hover,
									&:active,
									&:focus {
										text-decoration: none;
									}
								}	
								i {
									&[class^="ss-"] {
										font-size: 10px; 
									}
									&[class^="fa fa-"] {
										font-size: 16px; 
									}
									
								}
							}
							.tg-sr-drop {
								display: none;
								position: absolute;
								right: 10px;
								padding-top: 4px;
								z-index: 10;
								.tg-sr-drop-container {
									  background: #fff;
									  border: 1px solid #333;
									  padding: 4px;
									  white-space: nowrap;
									  &:before,
									  &:after {
									  	  position: absolute;
										  top: -5px;
										  right: 0;
										  bottom: 100%;
										  width: 0;
										  height: 0;
										  pointer-events: none;
										  border: solid transparent;
										  content: " ";
									  }
									  &:before {
									  	  margin-top: -2px;
										  margin-left: -16px;
										  border-color: transparent;
										  border-bottom-color: #333333;
										  border-width: 6px;
									  }
									  &:after {
									  	  right: 1px;
										  margin-left: -8px;
										  border-color: transparent;
										  border-bottom-color: #ffffff;
										  border-width: 5px;
									  }
									.tg-sr-list {
										margin: 0;
										padding: 0;
									  	font-size: 14px;
  										line-height: 19px;
										> li {
											list-style: none;
										  	position: relative;
										  	cursor: default;
										  	padding: 4px 6px 4px 30px;
											> span {
												
											}
										}
									}
								}
							}
							&:hover {
								cursor: pointer;
								.tg-sr-drop {
									display: block;
								}
							}
						}				
					}
					&.selected {
						> td {
							> .selectState {
								> label {
									> span {
										> i {
											
										}
									}
								}
							}
						}
					}
				}
			}
		}
	}
	.tg-sortable-rows {
		
	}
	.tg-passive-and-active-ledgers {
		
	}
	.tg-static-headers {
		
	}	
	.tg-fat-data-rows {
		table {
			> tbody {
				> tr {
					> th,
					> td {
						&.tg-toggle {
							cursor: pointer;
							position: relative;
							> span {
							  	top: 50%;
							  	position: absolute;
							  	&[class^="ss-"] {
							  		font-size: 10px;
								  	margin-top: -7px;
							  	}
							  	&[class^="fa fa-"] {
								  	font-size: 16px;
								  	margin-top: -8px;
							  	}
							  	&.ss-plus {
									display: block;
								}
								&.ss-hyphen {
									display: none; 
								}
								&.fa-plus {
									display: block;
								}
								&.fa-minus {
									display: none; 
								}
							}
						}
					}
					&.shown {
						> th,
						> td {
							&.tg-toggle {
								> span {
									&.ss-plus {
										display: none;
									}
									&.ss-hyphen {
										display: block;
									}
									&.fa-plus {
										display: none;
									}
									&.fa-minus {
										display: block;
									}
								}
							}
						}
						+ tr {
							> td {
								border-top-color: transparent;
								color: #999;
							}
						}
					}
				}
			}
		}
	}
	.tg-simple-table-filters {
		.tg-filter { 
			padding: 10px 0;
			overflow: hidden;
			margin-bottom: 10px;
			.tg-filter-btn {
				float: right;
				display: none;
			}			
		}
	}
	.tg-expandable-table-filters {
		.tg-filter {
			.tg-toggle-filter {
				float: right;
				font-size: @font-size-xs;
				line-height: @line-height-xs-computed;
			}
			.tg-filter-btn {
				float: right;
				clear: both;
				display: none;
			}
			.tg-expandable-filter {
				display: none;
				margin-top: 15px;
			}
			&.expanded {
				.tg-expandable-filter {
					display: block;
				}
				.tg-filter-btn {
					display: none;
				}
			}
		}
	}
	.tg-dependent-filters {
		.tg-filter {
			> a {
				display: none;
			}
			.tg-filter-step {
				.tg-filter-btn {
					> span {
						&:before {
							content: "Submit";
						}
					}
				}		
			}
		}
	}
	.tg-filter {
		padding: 10px 0;
		overflow: hidden;
		margin-bottom: 10px;	
		> form,
		.tg-filter-box {
			padding: 10px;
			border-radius: 3px;
			.clearfix();
			.form-group-inline {
				margin-right: 30px;
				margin-bottom: 10px;
				.checkbox-inline,
				.radio-inline {
					padding-left: 0px;
					> label {
						margin: 0;
					}
				}
			}
		}
	}
	.tg-responsive {
		.tg-r-selection {
			
		}
		.tg-r-wrapper {
			display: table;
			width: 100%;
			table-layout: fixed;
			.tg-r-static {
				display: table-cell;
				vertical-align: top;
				width: 200px;
				margin-bottom: 0;
				tr {
					&:hover {
						background: none;
					}
				}
				tbody {
					tr {
						td {
							font-weight: bold;
						}
					}
				}
			}
			.tg-r-scroll {
				vertical-align: top;
				overflow: auto;
				table {
					table-layout: fixed;
					margin-bottom: 0;
					tr {
						&:hover {
							background: none;
						}
					}
					th,
					td {
						width: 200px;
					}
				}
			}
		}
	}
}

table {
	&.dataTable {
	  	border-bottom: 1px solid #666;
	  	border-collapse: collapse;
	  	&.no-footer {
	  		border-bottom: 1px solid #666;
	  	}  
	  	> thead > tr > th,
	  	> thead > tr > td {
	  		border-bottom: 1px solid #666;
	  		padding: 8px;
	  	}
	  	> tbody > tr > th,
	  	> tbody > tr > td {
	  		padding: 8px; 
	  	}
	  	thead {
	  		.sorting {
			  	background-image: url("../images/sort_both.png");
			}
	  		.sorting_asc {
  			  	background-image: url("../images/sort_asc.png");
	  		}
	  		.sorting_desc {
			  	background-image: url("../images/sort_desc.png");
			}
	  	}
	}
}

table {
	&.dataTable {
		thead {
			th {
				position: relative;
				vertical-align: top;	
				> div {
					display: inline-block;
				}
			}
			.sorting_disabled {
				.ss-icon {
					display: none;
				}
				[class^="fa fa-"] {
					display: none;
				}
			}
			.sorting,
			.sorting_asc,
			.sorting_desc {
				background-image: none;
				color: @text-color;
				.ss-icon {
					font-size: 9px;
					font-weight: bold;
				  	top: 18px;
					margin-top: 0px;
					margin-left: 5px;
					display: inline-block;
					position: absolute; 
				}
				[class^="fa fa-"] {
					font-size: 16px;
					font-weight: bold;
				  	top: 14px;
					margin-top: 0px;
					margin-left: 5px;
					display: inline-block;
					position: absolute; 
				}
			}
			.sorting,
			.sorting_asc,
			.sorting_desc {
				.ss-icon {
					&.up {
					  margin-top: -8px;
					}
				}
				[class^="fa fa-"] {
					&.up {
					  margin-top: -8px;
					}
				}
			}	
			.sorting {	
				.ss-icon {
					color: #E6E6E6;
				}
				[class^="fa fa-"] {
					color: #E6E6E6;
				}
			}
			.sorting_asc {	
				.ss-icon {
					color: @link-color;
					&.up {
				  		display: inline-block;
					}
					&.down {
				 		display: none;
					}
				}
				[class^="fa fa-"] {
					color: @link-color;
					&.up {
				  		display: inline-block;
					}
					&.down {
				 		display: none;
					}
				}
			}
			.sorting_desc {	
				.ss-icon {
					color: @link-color;
					&.up {
				 		display: none;
					}
					&.down {
				  		display: inline-block;
					}
				}
				[class^="fa fa-"] {
					color: @link-color;
					&.up {
				 		display: none;
					}
					&.down {
				  		display: inline-block;
					}
				}
			}
		}
	}
}  
.dataTables_scroll {
	.dataTables_scrollBody {
	  	border-top: 1px solid #111111;
		table {
			&.dataTable {
				 > tbody > tr:first-child > th, 
				 > tbody > tr:first-child > td {
				  	border-top: none;
				}
				thead {
					.sorting,
					.sorting_asc,
					.sorting_desc {
						.ss-icon {
							display: none;
						}
						[class^="fa fa-"] {
							display: none;
						}
					}
				}
			}
		}
	}
	.dataTables_scrollHead {
		table {
			&.dataTable {
				> thead > tr > th,
				> thead > tr > td {
					border-bottom: none;
				}
			}
		}
	}
}



form {
	.form-group-inline {
	  	display: inline-block;
	  	margin-bottom: 0;
	  	vertical-align: middle;
	  	.form-control {
		  	display: inline-block;
		  	width: auto;
		  	vertical-align: middle;
		}
	}
}

@media(max-width:@screen-xs-max) {
	form {
		.form-group-inline {
		  	display: block;
		  	margin-bottom: 15px;
		  	vertical-align: middle;
		  	.form-control {
			  	display: block;
			  	width: 100%;
			  	vertical-align: middle;
			  	margin-bottom: 10px;
			}
		}
	}
}

@media(min-width:@screen-sm-min) {
	.tables-and-grids {
		.tg-selectable-rows {
			table {
				tbody {
					> tr {
						> td {
							.tg-sr-options {
								.tg-sr-drop {
									width: auto!important;
								}
							}		
						}	
					}
				}
			}
		}
		.tg-dependent-filters {
			.tg-filter {
				.tg-filter-box {
					.tg-filter-step {
						&.step-2 {
							display: none;
							.loading-icon {
								display: none;
								text-align: center;
							}
							&.loading {
								display: block;
								.loading-icon {
									display: block;
								}
								form {
									display: none;
								}
							}
							&.loaded {
								display: block;
								.loading-icon {
									display: none;
								}
								form {
									display: block;
								}
							}
						}
					}	
				}
			}
		}
	}
}

@media(max-width:@screen-xs-max) {
	.tables-and-grids {
		.table-responsive {
			overflow-y: auto;
		}
		.tg-selectable-rows {
			table {
				tbody {
					> tr {
						> td {
							.tg-sr-options {
								.tg-sr-display {
									.tg-sr-display-text {
										color: @link-color;
									}	
									i {
										display: none
									}
								}
								.tg-sr-drop {
									display: none;
									right: 0px;
									.tg-sr-drop-container {
										  &:before {
										  	right: 25px;
										  }
										  &:after {
										  	  right: 26px;
										  }
										  p {
										  	padding: 5px;
										  	margin: 0;
										  }
										.tg-sr-list {
											> li {
												padding: 5px;
											}
										}
									}
								}
								&:hover {
									cursor: pointer;
									.tg-sr-drop {
										display: none;
									}
								}
								&.opened {
									.tg-sr-drop {
										display: block;
									}
								}
							}	
						}
					}
				}
			}
		}
		.tg-dependent-filters {
			position: relative;
			.panel {
				background: transparent;
				margin: 0;
				.box-shadow(none);
				.panel-body {
					padding: 0;
				}
			}	
			.media-right {
				display: block;
				float: right;
			}
			.tg-filter {
				position: relative;	
				background: @white-base;
				text-align: center;
				overflow: hidden;
				padding: 0;
				margin: 0;
				> a {
					display: none;
					position: absolute;
					top: 0;
					padding: 0 10px;
					&:hover,
					&:focus,
					&:active {
						text-decoration: none;
					}
				}
				.tg-filter-drop {
					float: right;
					display: inline-block;
					position: relative;
				}
				.tg-filter-close {
					right: 0;
					color: @btn-default-bg;
					&:hover,
					&:focus {
						color: @btn-default-border;
					}
					&:active {
						color: @btn-default-bg-active;
					}					
					> i {
						&[class^="ss-"] {
							font-size: 10px;
						}
						
						&[class^="fa fa-"] {
							font-size: 14px;
						}
						
					}
				}
				.tg-filter-back {
					left: 0;
					> i {
						font-size: 10px;
						margin-right: 5px;
					}
				}
				.tg-filter-box {
					left: 0;
					width: 100%;
					position: absolute;
					text-align: left;
					.transition(left .3s);
					.form-group-inline {
						margin-right: 0;
					}
					.tg-filter-step {
						overflow: hidden;
						position: absolute;
						width: 100%;
						opacity: 0;
						padding: 10px;
						.tg-filter-btn {
	
						}
						&.step-1 {
							right: -100%;
							.tg-filter-btn {
								> span {
									&:before {
										content: "Next...";
									}
								}
							}
						}
						&.step-2 {
							right: -200%;
							display: block;
							&.loading,
							&.loaded {
								.loading-icon {
									display: none;
								}
								form {
									display: block;
								}
							}
							.tg-filter-btn { 
								> span {
									&:before {
										content: "Done";
									}
								}
							}
						}
					}
				}
				&.opened {
					position: absolute;	
					border: 1px solid #ccc;
					width: 100%;
					> a {
						padding: 10px;
					}
					.tg-filter-drop {
						float: none;
						color: @text-color;
					}
					.tg-filter-close {
						display: inline-block;
					}
					.tg-filter-back {
						display: inline-block;
					}
				}
				&.opened-1 {	
					.tg-filter-box {
						left: -100%;
						.transition(left .3s);
						.tg-filter-step {	
							&.step-1 {
								opacity: 1;
							}
							&.step-2 {
								opacity: 0;
							}
						}
					}
				}
				&.opened-2 {	
					.tg-filter-box {
						left: -200%;
						.transition(left .3s);
						.tg-filter-step {
							&.step-1 {
								opacity: 0;
							}
							&.step-2 {
								opacity: 1;
							}
						}
					}
				}
				&.filtered {
					.tg-filter-drop {
						display: none;
					}
					.tg-filter-clear,
					.tg-filter-edit {
						  display: inline-block;
						  position: relative;
						  float: right;
						  padding: 0 10px;
					}
					.tg-filter-edit {
						  border-left: 1px solid #ccc;
					}
					&.opened {
						.tg-filter-clear,
						.tg-filter-edit {
							display: none;
						}
						.tg-filter-drop,
						.tg-filter-close,
						.tg-filter-back {
							display: inline-block;
						}
					}
				}
				.loading-icon {
					display: none;
				}
			}
		}
		.tg-filter {
			.tg-filter-box {
				padding: 0;
				background: transparent;
				.clearfix();
				.form-group-inline {
					+ .form-group-inline {
						margin-left: 0;
					}
					.checkbox-inline,
					.radio-inline {
						padding-left: 0px;
						> label {
							margin: 0;
						}
					}
				}
			}
		}
		.tg-responsive {
			.tg-r-selection {
				
			}
			.tg-r-wrapper {
				.tg-r-static {
					width: 50%;
				}
				.tg-r-scroll {
					table {
						th,
						td {
							display: none;
							&.show-phone {
								display: table-cell;
							}
						}
						th {
							.tg-r-cell {
							  display: none;
							}
						}
					}
				}
			}
		}
	}
	.tables-and-grids {
		.table-responsive {
			.table {
				> thead {
					> tr {
						> th,
						> td{
							white-space: nowrap;
						}
					}
				}
				> tbody {
					> tr {
						> td {
							white-space: nowrap;
						}
					}
				}
			}
		}
	}
}