//
// Tables
// --------------------------------------------------




// Baseline styles

.table {
	font-size: @font-size-small;
  border-bottom: 1px solid #666;
  // Bottom align for column headings
  > thead > tr > th {
    border-bottom: 1px solid @table-border-edge-color;
  }

	> tbody {
		> tr {
			&:first-child {
				> th,
				> td {
					border-top: 1px solid @table-border-edge-color;
				}
			}
			&:hover {
				background-color: @table-bg-hover;
			}
			&.odd {
				&,
				& + .insert {
					background-color: #fff;
					&:hover {
						background-color: @table-bg-hover;
					}					
				}				
			}
			&.even {
				&,
				& + .insert {
					background-color: @table-bg-accent;
					&:hover {
						background-color: @table-bg-hover;
					}				
				}
			}
			&.odd,
			&.even {
				& + .insert {
					> td {
						border-top: 0;
					}
				}				
			}
		}
	}	
}


// Bordered version
//
// Add borders all around the table and between all the columns.

.table-bordered {
  > thead > tr {
    > th,
    > td {
      border-bottom-width: 0;
    }
  }
	> tbody {
		> tr {
			&:first-child {
				> th,
				> td {
					border-top-color: @table-border-color;
				}
			}
		}
	}	
}

// Dark header version
//
// Dark grey background for table header.

.table-dark-th {
  > thead > tr {
    background: #666;
		> th,
    > td {
      color: #fff;
    }
  }	
}

// Zebra-striping
//
// Default zebra-stripe styles (alternating gray and transparent backgrounds)

.table-striped,
.table-hybrid {
	border-bottom: 0;
	> thead > tr {
		> th {
			border-bottom-color: #ccc;
		}
	}
  > tbody > tr {
		&:first-child {
			> th,
			> td {
				border-top-color: #ccc;
			}
		}
		&:nth-of-type(odd) {
			background-color: #fff;
			&:hover {
				background-color: @table-bg-hover;
			}
		}
		&:nth-of-type(even) {
			background-color: @table-bg-accent;
			&:hover {
				background-color: @table-bg-hover;
			}
		}
	}
	&.table-bordered {
		> thead,
		> tbody,
		> tfoot {
			> tr {
				> th,
				> td {
					border: 1px solid @table-border-color;
				}
			}
		}
	}
}

// hybrid styles
//
// Zebra-striping and dark top and bottom border

.table-hybrid {
	border-bottom: 1px solid #666;
	> thead > tr {
		> th {
			border-bottom-color: #666;
		}
	}
	> tbody > tr {
		&:first-child {
			> th,
			> td {
				border-top-color: #666;
			}
		}
	}
}

// Table backgrounds
//
// Exact selectors below required to override `.table-striped` and prevent
// inheritance to nested tables.

// Generate the contextual variants
.table-row-variant-pl(active; @table-bg-active);
.table-row-variant-pl(success; @state-success-bg);
.table-row-variant-pl(info; @state-info-bg);
.table-row-variant-pl(warning; @state-warning-bg);
.table-row-variant-pl(danger; @state-danger-bg);
