// global nav styles
.gn-container {
	position: relative;
	z-index: 100;
	& + .container {
		margin-top: 20px;
	}
}

.gn-container * {
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
}

.global-nav-oz {
	border-bottom: 1px solid #eaeaea;
	height: 87px;
	position: relative;
	z-index: 3;
	background-color: #fff;	
	[class^="col-"] {
		position: static;
		min-height: 0;
		.box-sizing(border-box);
	}
	
	a {
		text-decoration: none;
	}
}
.gn-no-nav {
	.global-nav-oz {
		height: 54px;
	}
	.gn-logo {
		a {
			width: 109px;
			height: 26px;
			background-image: url('../images/stx-staticlogo-drk-nopad-109x26.png');
		}
	}
}

.home-page {
	position: relative;
	z-index: 3;
	.sticky-header-active {
		background-color: transparent;
	}
	.global-nav-oz {
		border-bottom: 1px solid rgba(234, 234, 234, 0.2);
		background-color: transparent;
		.transition(background-color .3s ease);
		.gn-icon {
			> .gn-icon-container {
				color: #fff;
			}
		}
		.gn-menu-item-1lvl {
			> a {
				color: #fff;
			}
		}
	}
	.gn-logo {
		a {
			background-image: url('../images/stx-staticlogo-wht-nopad-150x36.png');
		}
	}
	.gn-drop-overlay {
		top: 0;
	}
	
	.gn-container.gn-pre-sticky {
		.sticky-header-active {
			.global-nav-oz {
				.gn-icon {
					> .gn-icon-container {
						color: #fff;
					}
				}
				.gn-menu-item-1lvl {
					> a {
						color: #fff;
					}
				}
				.gn-menu-pulled {
					.gn-menu-item-1lvl {
						> a {
							color: @link-color;
						}
					}
				}
			}
			.gn-logo {
				a {
					background-image: url('../images/stx-staticlogo-wht-nopad-150x36.png');
				}
			}
			.gn-drop-overlay {
				top: 0;
			}
		}
	}
	
	.sticky-header-active,
	.gn-container.gn-post-sticky {
		.global-nav-oz {
			background-color: #fff;
		}
		.gn-icon {
			> .gn-icon-container {
				color: @text-color;
			}
		}
		.gn-menu-item-1lvl {
			> a {
				color: @text-color;
			}
		}
		.gn-logo {
			a {
				background-image: url('../images/stx-staticlogo-drk-nopad-150x36.png');
			}
		}
		.gn-drop-overlay {
			top: 88px;
		}
	}
}
.gn-logo,
.gn-menu {
	float: left;
}
.gn-logo {
	padding: 10px;
	background: transparent;
	margin: 3px 0 0 -8px;
	position: relative;
	a {
		display: block;
		width: 150px;
		height: 36px;
		border-radius: 3px;
		text-align: center;
		font-size: 0;
		line-height: 36px;
		background-image: url('../images/stx-staticlogo-drk-nopad-150x36.png');
		background-repeat: no-repeat;
	}
}
.gn-app {
	float: left;
	font-size: 14px;
	line-height: 36px;
	padding: 10px;
	margin-top: 3px;
	> a {
		padding-left: 1px;
		position: relative;
		&:before {
			content: "|";
			color: #333;
			position: absolute;
			left: -10px;
		}
	}
	@media (max-width: @screen-sm-max) {
		line-height: 26px;
		margin-top: 1px;
	}
} 
.gn-no-nav,
.sticky-body {
	.gn-app {
		line-height: 26px;	
		display: block;	
	}
}
.unsticky-body {
	.gn-app {
		display: none;	
	}
}
.gn-icon,
.gn-search,
.gn-store {
	margin-top: 15px;
}
.gn-icon {
	display: inline-block;
	vertical-align: top;
	height: 20px;
	margin-left: 28px;
	> .gn-icon-container {
		display: block;
		color: @text-color;
		cursor: pointer;
		> span {
			display: inline-block;
			width: 16px;
			height: 16px;
			line-height: 20px;
			padding-top: 4px;
			vertical-align: top;
			&.gn-ic-text {
				width: auto;
				font-size: @font-size-xxs;
			}
			&[class^="fa fa-"]{
				font-size: 18px;
				line-height: 18px;
			}
		}
		& {
			text-decoration: none;
		}
		&:hover {
			text-decoration: none;
		}
	}
	&:hover {
		> .gn-icon-container {
			> span {
				color: @link-color;
			}
		}
	}
}
.gn-user {
	position: relative;
	> .gn-icon-container {
		position: relative;
		padding-right: 18px;
		> i {
			font-size: 12px;
			color: @link-color;
			display: inline-block;
			width: 12px;
			height: 20px;
			line-height: 20px;
			vertical-align: top;
			position: absolute;
			top: 0;
			//left: 0;
			right: 0;
			padding: 5px 0 0 0;
			background-color: rgba(255,255,255,0);
			&[class^="fa fa-"]{
				line-height: 15px;
				font-size: 20px;
				width: 14px;
			}
		}
		
	}
	&:hover,
	&.gn-user-pulled {
		.gn-user-drop-container {
			display: block;
		}		
	}
	.gn-user-drop-container {
		position: absolute;
		display: none;
		min-width: 160px;
		right: -2px;
		padding: 8px 0 0;
		z-index: 101;
	}
	.gn-user-drop {
		background: #ffffff;
		border: 1px solid #333333;
		color: @text-color;
		padding: 8px 0 0;
		text-align: right;
		&:before,
		&:after {
			bottom: 100%;
			right: 0;
			border: solid transparent;
			content: " ";
			height: 0;
			width: 0;
			position: absolute;
			pointer-events: none;
			top: -5px;
		}
		&:after {
			right: 1px;
			border-color: transparent;
			border-bottom-color: #ffffff;
			border-width: 7px;
			margin-left: -8px;
		}
		&:before {
			border-color: transparent;
			border-bottom-color: #333333;
			border-width: 8px;
			margin-left: -16px;
			margin-top: -2px;
		}
		> p,
		> a	{
			display: block;
			font-size: 12px;
			line-height: 15px;			
			padding: 0 10px;
			margin: 0 0 8px;
			white-space: nowrap;
			vertical-align: top;
		}
		> hr {
			margin: 0 0 8px;
			border: 0;
			border-top: 1px solid #e6e6e6;
		}
		a {
			&:hover,
			&:active,
			&:focus {
				text-decoration: underline;
			}
		}
	}
}
.gn-cart {
	position: relative;
	> a {
		> i {
			position: absolute;
			top: 0;
			right: -17px;
			min-width: 20px;
			height: 20px;
			border-radius: 50%;
			background: @red-base;
			font-size: 12px;
			font-weight: bold;
			font-style: normal;
			line-height: 20px;
			text-align: center;
			color: #fff;
		}
	}
}
.gn-globe {
	.globe-icon {
		background: none;
		padding-left: 0;
  		margin-bottom: 0;
	}
}

.gn-search {
	//width: 270px;
	//background: rgba(200,50,100,.4);
	position: relative;
	form {
		margin: 0;
		> * {
			.box-sizing(border-box);
			height: 25px;
			vertical-align: top;
		}
	}
	.gn-search-btn {
		float: right;
		width: 44px;
		border-radius: 0 3px 3px 0;
		font-size: 16px;
		line-height: 25px;
		padding: 0;
		> span {
			width: 16px;
			display: inline-block;
			vertical-align: middle;
			&[class^="fa fa-"]{
			    vertical-align: initial;
			    position: relative;
    			top: -2px;
			}
		}
	}
	.gn-search-input {
		border-radius: 3px 0 0 3px;
		border: 1px solid @text-color;
		border-right: 0;
		font-size: 13px;
		line-height: 18px;
		margin: 0;
		padding: 3px 36px 2px 10px;
		width: 230px;
		.box-shadow(none);
		color: #ccc;
		&:focus {
			color: #333;
		}
		&::-ms-clear {
			display: none;
		}
	}
	.gn-search-clear {
		color: #999999;
		width: 26px;
		font-size: 13px;
		line-height: 27px;
		position: absolute;
		right: 54px;
		top: 0;
		text-align: right;
		> span {
			width: 13px;
    		display: inline-block;
    		vertical-align: middle;
    		&[class^="fa fa-"] {
	    		font-size: 16px;
	    		margin-top: -5px; 
    		}  
		}
	}
}
.gn-menu {
	//width: 640px;
	height: 28px;  
	//background: rgba(200,50,200,.4);
	clear: left;
	> ul {
		list-style: none;
		margin: 0;
		padding: 0;
		> li {
			float: left;			
			list-style: none;
			&.gn-menu-pulled {
				.gn-dropdown {
					display: block;
				}
				.gn-menu-item-1lvl {
					> a {
						color: @link-color;
					}
					> [class^="ss-"] {
						-ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=100)";
						filter: alpha(opacity=100);
						opacity: 1;
					}
					> [class^="fa fa-"] {
						-ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=100)";
						filter: alpha(opacity=100);
						opacity: 1;
					}
				}
			}
		}
	}
}
.gn-dropdown {
	position: absolute;
	left: 0;
	right: 0;
	top: 100%;
	background: #eee;
	display: none;
	.box-shadow(0 2px 2px rgba(0,0,0,0.1));
}

.gn-menu-item-1lvl {
	position: relative;
	> img[title="Edit item"] {
		position: relative;
		z-index: 3;
	}
	> a {
		display: block;
		height: 28px;
		font-size: 12px;
		font-weight: bold;
		line-height: 28px;
		text-transform: uppercase;
		color: @text-color;
		padding-right: 40px;
		margin: 0;
		cursor: pointer;
		//.transition(color .3s ease);
		position: relative;
		z-index: 2;
		background-color: rgba(255,255,255,0);
		&:hover {
			color: @link-color;
			text-decoration: none;
		}
	}
	> [class^="ss-"] {
		font-size: 9px;
		color: @link-color;
		position: absolute;
		z-index: 1;
		right: 25px;
		top: 0;
		line-height: 28px;
		-ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
		filter: alpha(opacity=0);
		opacity: 0;		
		width: 9px;
		//.transition(all .3s ease);
	}
	> [class^="fa fa-"] {
		font-size: 16px;
		color: @link-color;
		position: absolute;
		z-index: 1;
		right: 25px;
		top: 0;
		line-height: 28px;
		-ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
		filter: alpha(opacity=0);
		opacity: 0;		
		width: 9px;
		//.transition(all .3s ease);
		&.directup {
			color: #eee;
			line-height: 29px;
			padding-top: 10px;
		}
	}
	> .directup {
		color: #eee;
		line-height: 29px;
		padding-top: 12px;
	}
}
.gn-menu-item-2lvl {
	font-size: 14px;
	>  p {
		margin-bottom: 7px;
	}
}


.gn-dropdown-menu {
	min-height: 320px;
	> [class^="gn-col-"] {
		> .gn-row {
			padding-bottom: 30px;
			&:first-child {
				padding-top: 30px;
			}
		}
	}
	> [class^="col-"] {
		> .row {
			padding-bottom: 30px;
			&:first-child {
				padding-top: 30px;
			}
		}
	}
	> .col-md-3 {
		position: relative;
		.gn-promo {			
			height: 100%;
		}
		.gn-promo-content {
			padding: 30px 15px;
			position: relative;
		}
	}
	ul {
		margin: 0;
		padding: 0;
		list-style: none;
		> li {
			list-style: none;
			font-size: 12px;
			line-height: 15px;
			padding: 3px 0 2px;
		}
	}
}
.gn-promo {
	min-height: 320px;
}


.gn-side-overlay,
.gn-drop-overlay {
	position: absolute;
	right: 0;
	left: 0;
	z-index: -2;
	opacity: 0;
	background: rgba(0,0,0,.85);
	visibility: hidden;
}
.gn-side-overlay {
	top: 0;	
}
.gn-drop-overlay {
	top: 88px;	
	-webkit-transition: opacity 0.3s ease, visibility 0s 0.3s ease;
	-moz-transition: opacity 0.3s ease, visibility 0s 0.3s ease;
	-ms-transition: opacity 0.3s ease, visibility 0s 0.3s ease;
	-o-transition: opacity 0.3s ease, visibility 0s 0.3s ease;
	transition: opacity 0.3s ease, visibility 0s 0.3s ease;
}
.gn-side-opened {
	.gn-side-overlay {
		z-index: 2;
		opacity: 1;
		visibility: visible;
		.transition(opacity .3s ease);
	}
}
.gn-drop-pulled {
	.gn-drop-overlay {		
		opacity: 1;
		visibility: visible;
		.transition(opacity .3s ease);
	}
}

.gn-store {
	width: 150px;
	margin: -2px 0 0;
	> a {
		display: block;
		height: 26px;
		padding: 0 10px;
		font-size: 12px;
		line-height: 26px;
		.box-sizing(border-box);
	}
}

.gn-menu ul li {
  	list-style: none;
}

.gn-logo .seagate-portal-logo {
  	display: inline-block;
  	top: 17px;
  	left: 175px;
  	font-size: 20px;
  	color: #333;
}
.home-page .gn-logo .seagate-portal-logo {
	color: #fff; 
}

.sticky-header-top .global-nav-oz{
	height: 65px;
}

.sticky-header-top .global-nav-oz .gn-menu {
	display: none;
}

.sticky-header-top .global-nav-oz .gn-store {
	display: none;
}

.sticky-header-active .gn-logo .seagate-portal-logo {
	color: #333;
}



