.horizontal-sub-navigation {
	.filters-n-sorting {
		padding: 7px 12px 0;
	}
}

.hsn-to-dropdown {
	> .hsn-to-drop {
		.hsn-to-item {
			> a {
				&.fns-toggle {
					border: 1px solid #f7f7f7;
					margin: 0;
					.transition(all .2s);
					border-radius: 3px;
					&:hover {
						border: 1px solid #c6c6c6;
						.box-shadow(0 1px 1px rgba(0,0,0,0.1));
						margin: 0 12px;
					}
					&[aria-expanded="true"] {
						background: #eee;
						.box-shadow(inset 0 1px 2px 0 rgba(0,0,0,0.1));
						border: 1px solid #ddd;
						margin: 0 12px;
					}
				}			
			}
		}
		&.fns-toggle-container {
			.hsn-to-item {
				&:hover {
					background: transparent;
				}
			}
		}
	}
}
@media (min-width:@screen-sm-min) {
	.horizontal-sub-navigation {
		.filters-n-sorting {
			padding: 7px 12px 15px;			
		}
	}
}
@media (min-width:@screen-md-min) {
	.horizontal-sub-navigation {		
		.filters-n-sorting {
			padding: 12px 0;
		}
	}
	.hsn-to-dropdown {
		> .hsn-to-drop {
			.hsn-to-item {
				> a {
					&.fns-toggle {
						border: 0;
						padding: 3px 6px 2px;				
						margin: 6px 0 5px;
						border: 1px solid #fff;
						&:hover {
							border: 1px solid #c6c6c6;
							.box-shadow(0 1px 1px rgba(0,0,0,0.1));
							margin: 6px 0 5px;
						}
						&[aria-expanded="true"] {
							background: #eee;
							.box-shadow(inset 0 1px 2px 0 rgba(0,0,0,0.1));
							border: 1px solid #ddd;
							margin: 6px 0 5px;
						}
						&:after {
							content: none; 
						}
					}					
				}
			}
		}
	}
}

@media (max-width:@screen-sm-max) {
	.bsr-filter {
		display: inline-block;
		z-index: 2;
	}

	.hsn-s-results {
		.fns-toggle-container {
			display: none;
		}
		.filter-sorting {
			display: block;
		  	position: absolute;
		  	width: 100%;
		  	left: 0;
		  	overflow: hidden;
		  	margin-top: 10px;
			.filters-n-sorting {
				position: absolute;
			  	right: -100%;
			  	width: 100%;
			  	background: #fff;
			  	z-index: 10;
			  	border: 1px solid #ccc;
			  	padding: 7px 12px;
			  	.fs-submit {
			  		margin: 10px auto;
			  	}
			}
			&.opened {
				.filters-n-sorting {
					right: 0;
					.transition(right .3s ease);
				}
			}
		}
	}
}
