//
// Buttons
// --------------------------------------------------


// Base styles
// --------------------------------------------------

.btn {
	text-transform: uppercase;
	white-space: normal;
	.button-size-pl(@padding-base-vertical; (@padding-base-vertical - 1); @padding-base-horizontal; @btn-font-size-base; @btn-line-height-base);
	.transition(~"background-color .3s, color .3s");
  &:active,
  &.active {
    .box-shadow(none);
		&:focus,
    &.focus {
			outline: 0;
		}
  }
}

// Alternate buttons
// --------------------------------------------------

.btn-default {
	.button-variant-pl(@btn-default-color; @btn-default-color-active; @btn-default-bg; @btn-default-bg-active; @btn-default-border; @btn-default-border-active);
	&.btn-outline {
		.button-variant-pl(@btn-default-outline-color; @btn-default-outline-color-active; @btn-default-outline-bg; @btn-default-outline-bg-active; @btn-default-outline-border; @btn-default-outline-border-active);
		&:hover,
		&:focus,
		&.focus {
			color: @btn-default-outline-color-hover;
		}
		&:active {
			color: @btn-default-outline-color-active;
		}
	}
}
.btn-primary {
	.button-variant-pl(@btn-primary-color; @btn-primary-color-active; @btn-primary-bg; @btn-primary-bg-active; @btn-primary-border; @btn-primary-border-active);
}
.btn-purchase {
	.button-variant-pl(@btn-purchase-color; @btn-purchase-color-active; @btn-purchase-bg; @btn-purchase-bg-active; @btn-purchase-border; @btn-purchase-border-active);
}



// Link buttons
// -------------------------

// Make a button look and behave like a link
.btn-link {
  color: @link-color;
  font-weight: normal;
  border-radius: 0;

  &,
  &:active,
  &.active,
  &[disabled],
  fieldset[disabled] & {
    background-color: transparent;
    .box-shadow(none);
  }
  &,
  &:hover,
  &:focus,
  &:active {
    border-color: transparent;
  }
  &:hover,
  &:focus {
		color: @link-color;
	}
  &:active {
		color: @link-active-color;
	}
  &:hover,
  &:active,
  &:focus {
    text-decoration: @link-hover-decoration;
    background-color: transparent;
  }
  &[disabled],
  fieldset[disabled] & {
    &:hover,
    &:focus {
      color: @btn-link-disabled-color;
      text-decoration: none;
    }
  }
}

// Outline buttons
// -------------------------



// Button Sizes
// --------------------------------------------------

.btn-lg {
  // line-height: ensure even-numbered height of button next to large input
	.button-size-pl(@padding-large-vertical; (@padding-large-vertical - 2); @padding-large-horizontal; @btn-font-size-large; @btn-line-height-large);
	border-radius: @border-radius-base;
}
.btn-sm {
  // line-height: ensure proper height of button next to small input
  .button-size-pl(@padding-small-vertical; (@padding-small-vertical - 3); @padding-small-horizontal; @btn-font-size-small; @btn-line-height-small);
}
.btn-xs {
  .button-size-pl(@padding-xs-vertical; @padding-xs-vertical; @padding-xs-horizontal; @btn-font-size-xs; @btn-line-height-xs);
}


// Block button
// --------------------------------------------------

.btn-block {
  display: block;
  width: 100%;
}

// Vertically space out multiple block buttons
.btn-block + .btn-block {
  margin-top: 5px;
}

// Specificity overrides
input[type="submit"],
input[type="reset"],
input[type="button"] {
  &.btn-block {
    width: 100%;
  }
}
