.stay-informed {
	  float: right;
	  z-index: 10;
	  position: relative;
	  color: #666;
	  margin-top: 4px;
	  &.dark {
		.stay-informed-toggle {
			background: #3e4d52;
			color: @white-base;
			.stay-informed-toggle-icon {
				&:before {
				  background: #2f3a3e;
			 	}
			}
		}
	}
	&.gray {
		.stay-informed-toggle {
			background: #666;
			color: @white-base;
			.stay-informed-toggle-icon{
				&:before {
				  background: #555;
			 	}
			}
		}
	}
	&.green {
		.stay-informed-toggle{
			background: #548738;
			color: @white-base;
			.stay-informed-toggle-icon{
				&:before {
				  background: #385E24;
			 	}
			}
		}
	}
	.stay-informed-toggle {
		display: block;
		color: @white-base;
		background: #007a9e;
		@media (min-width: @screen-md-min) {
			border-radius: 3px;
		}
		.stay-informed-toggle-icon {
			display: inline-block;
			position: relative;
			z-index: 2;
			margin-left: 5px;
			&:before {
				content: "";
				display: block;
				position: absolute;
				width: 24px;
				height: 24px;
				background: #005c7b;
				border-radius: 100%;
				top: 50%;
				z-index: -1;
				margin-top: -12px;
				left: 50%;
				margin-left: -12px;
			}
			.ss-navigateup {
				display: none;
				position: relative;
				top: 1px;
			}
			.ss-navigatedown {
				display: block;
				position: relative;
				top: 3px;
			}
			.fa-chevron-up {
				display: none;
				position: relative;
				top: -1px;
			}
			.fa-chevron-down {
				display: block;
				position: relative;
				top: 0;
			}
		}
		&:hover,
		&:active,
		&:focus {
			text-decoration: none;
		}
	}
	&.opened {
		.stay-informed-toggle {
			color: @white-base;
			.stay-informed-toggle-icon {
				.ss-navigateup {
					display: block;
				}
				.ss-navigatedown {
					display: none;
				}
				.fa-chevron-up {
					display: block;
				}
				.fa-chevron-down {
					display: none;
				}
			}
		}
		.stay-informed-box { 
			.transition(height .3s);
			@media (min-width: @screen-md-min) {
				margin-top: 4px;
			}
			.stay-informed-content {	
				.stay-informed-close {
					cursor: pointer;
					&:hover {
						color: #bbb;
					}
					&:active {
						color: @text-color;
					}
				}
			}
		}
	}
	.stay-informed-box {
		height: 0;
		overflow: hidden;
		position: absolute;
		width: 370px;
		right: 0;
		background: #f0f0f0;
		border-radius: 3px;
		display: block;
		.transition(height .3s);
	  .stay-informed-content {
			padding: 15px;
			overflow: hidden;
			.stay-informed-title {
				p {
					padding-right: 30px;
				}
				.stay-informed-close {
					float: right;
					color: #ddd;
					&[class^="fa fa-"] {
				    	font-size: 18px;
				   	}
				}
			}
			form {
				.validation-message {
					display: none;
				}
				.has-error {
					.validation-message {
						display: block;
					}
				}
				button {
					+ p {
						margin: 7px 0;
					}
				}
			}
		}
	}
}
.horizontal-sub-navigation {
  position: relative;
  background: #f7f7f7;
	font-size: @font-size-small;
	line-height: @line-height-small-computed;
	ul {
		padding: 0;
	}
	li {
		list-style: none; 
	}
	+ .tab-content {
		padding: 12px;
	}	
}

.hsn-to-display {
	position: relative;
	background: @link-color;
	color: #fff;
	border-radius: 3px;
	> p {
		padding: 10px 12px 9px;
		margin-bottom: 0;
		margin-right: 60px;
	}
	
	.dropdown-toggle {
		top: 0;
		position: absolute;
		right: 0;
		text-decoration: none;
		width: 100%;
		height: 100%;
		&:hover,
		&:active {
			> em {
				background: @link-hover-color;
			}
		}
		> em {
			position: absolute;
			width: 60px;
			height: 100%;
			border-radius: 0 3px 3px 0;
			top: 0;
			right: 0;
			&:before {
				content: "";
				display: block;
				background: @link-divider-color;
				width: 1px;
				height: 100%;
				position: absolute;
				top: 0;
				left: 0;
			}
		}
		.ss-icon {
			color: #fff;
			position: absolute;
			top: 50%;
			left: 50%;
			margin: -7px 0 0 -7px;
			width: 14px;
			height: 14px;
			font-size: 14px;
			line-height: 19px;
			&.toggle-down {
				display: block;
			}
			&.toggle-up {
				display: none;
			}
		}
		[class^="fa fa-"] {
		    color: #fff;
		    position: absolute;
		    top: 50%;
		    left: 50%;
		    margin: -8px 0 0 -8px;
		    width: 16px;
		    height: 16px;
		    font-size: 16px;
		    line-height: 16px;
			&.toggle-down {
				display: block;
			}
			&.toggle-up {
				display: none;
			}
		}
	}
}
.hsn-to-dropdown-box {
	overflow: hidden;
	.box-shadow(0 1px 3px rgba(0,0,0,.25));
	border-radius: 3px;
}

.hsn-to-dropdown {
	position: relative; 
	background: #f7f7f7;
	display: none; 
	margin: 8px 0;
	ul {
		margin: 0;
	}
	> .hsn-to-drop {
		.hsn-to-item {
			position: relative;
			> a {
				display: block;
				padding: 4px 60px 4px 12px;				
			}
			> span {
				position: absolute;
				top: 0;
				right: 0;
				left: 0;
				bottom: 0;
				cursor: pointer;
				&[class^="ss-"] {
					font-size: 10px;
					&:before {
						margin-top: -5px;
					}
				}
				&[class^="fa fa-"] {
					font-size: 18px;
					&:before {
						margin-top: -9px;
					}
				}
				&:before {
					position: absolute;
					right: 25px;
					top: 50%;
				}
			}
		}
		.hsn-to-side {
			position: absolute;
			left: 100%; 
			width: 100%;
			top: 0;
			background: #f7f7f7;
			display: none;
			.hsn-back {
				padding: 4px 10px;
				display: block;
				&:hover,
				&:active,
				&:focus {
					text-decoration: none;
					outline: 0;
				}
				> span {
					margin-right: 8px;
					&[class^="ss-"]{
						font-size: 10px;
					}
					&[class^="fa fa-"]{
						font-size: 18px;
						vertical-align: middle;
					}
					
				}
			}
			> ul {				
				> li {
					&.active {
						> a {
							background: @link-selected-color;
							color: #fff;
							&:hover,
							&:active {
								background: @link-selected-color;
							}
						}
					}
					> a {
						color: @text-color;
						display: block;
						padding: 4px 12px;
						&:hover,
						&:active,
						&:focus {
							text-decoration: none;
							outline: 0;
						}
						&:hover {
							background: @link-hover-bg;
						}
						&:active {
							background: @link-active-color;
							color: #fff;
						}
						.hsn-img {
							display: none;
						}
					}
				}
			}
		}
	}		
}

.hsn-opened {
	.hsn-to-display {
		.dropdown-toggle {
			> em {
				background: @link-hover-color;
			}
			.ss-icon {	  				
					&.toggle-down {
						display: none;
					}
					&.toggle-up {
						display: block;
					}
				}
			[class^="fa fa-"] {	  				
					&.toggle-down {
						display: none;
					}
					&.toggle-up {
						display: block;
					}
				}
		}
	}
	.hsn-to-dropdown {
		display: block;
		left: 0;
		.transition(all .3s ease);
		> .hsn-to-drop {
			&.active,
			&.hsn-drop-active {
				.hsn-to-item {
					&,
					&:hover {
							background: @link-selected-color;
							color: #fff;
							> a {
								color: #fff;
							}
						}
						
				}
			}
			.hsn-to-item {
				&:hover {
					background: @link-hover-bg;
					 a {
						
					 }
				}
				&:active {
					background: @link-active-color;
					 > a {
						color: #fff;
						text-decoration: none;
					 }
					 > span {
						color: #fff;
					 }
				}
				> a {
					color: @text-color;
					&:hover,
					&:active,
					&:focus {
						text-decoration: none;
						outline: 0;
					}
				}
			}
		}
	}
}

.hsn-sided {
	.hsn-to-dropdown {
		left: -100%;
		.transition(all .3s ease);
		.hsn-to-drop {
			.hsn-to-item {					
			}
			&.hsn-drop-opened {
				.hsn-to-side {
					display: block;
				}
			}
		}
	}
}
.hsn-s-results {
	margin-bottom: 1em;
}
@media(max-width:@screen-sm-max) {
	.hsn-sided,
	.hsn-opened {
		.hsn-to-dropdown {
			>.hsn-to-drop {
				.hsn-to-side {
					display: none!important;
				}
				&.hsn-drop-opened {
					.hsn-to-side {
						display: block!important;
					}
				}
			}
		}
	}
	.stay-informed {
		margin-bottom: 10px;
		float: none;
		.stay-informed-toggle {
			padding-left: 12px;
			text-align: left;
			.stay-informed-toggle-icon {
				float: right;
			}
		}
		.stay-informed-box {
			width: 100%;  
		}
	}
}


@media (min-width:@screen-md-min) {
	.horizontal-sub-navigation {
		+ .tab-content {
			padding: 17px;
		}		
	}
	.hsn-sided,
	.hsn-opened {
		.hsn-to-dropdown {
			.transition(none);
			height: auto !important;
			left: 0;
			>.hsn-to-drop {
				&.active,
				&.hsn-drop-active {
					.hsn-to-item {
						background: transparent;
						&:hover,
						&:active,
						&:focus {
							background: transparent;
							outline: 0;
						}
						&:hover {
							a {
								color: @link-color;
								&:after {
									background: @link-color;
									
								}
							}
						}
						> a {
							color: @link-selected-color;
							&:after {
								background: @link-selected-color;
								.opacity(1);								
								z-index: 12;
							}
						}
					}
					.hsn-to-side {
						display: block;
						z-index: 1;
						> ul {
							> li {
								&.active {	
									> a {
										color: @link-selected-color;
										&:hover {
											color: @link-color;
										}
									}
								}
								> a {
									&:hover,
									&:active,
									&:focus {
										background: transparent;
										outline: 0;
									}
									&:active {
										color: @link-active-color;
									}
									&:hover {
										color: @link-color;
									}
								}
							}
						}
					}
				}
				.hsn-to-item {
					> a {
						color: @link-selected-color;
						font-weight: bold;
					}
					&:hover,
					&:active,
					&:focus {
						background: transparent;
						outline: 0;
					}
					&:active {
						> a {
							color: @link-active-color;
						}
					}
				}
			}
		}
	}
	.hsn-complex {
		.hsn-to-dropdown {
			.hsn-to-drop {
				.hsn-to-side {
					text-align: center;
					> ul {
						overflow: hidden;
						margin: 0 auto;
						display: inline-block;
						padding-top: 15px;
						> li {
							> a {
								.hsn-img {
									display: block;
									width: 40px;
									height: 40px;
									margin: 0 auto 10px;
									
								}
							}
						}
					}
				}
			}
		}
	}
	.hsn-to-dropdown-box {
		overflow: inherit;
		font-size: 0;
		.box-shadow(none);	
	}
	.hsn-sided {
		.hsn-to-dropdown-box {
			min-height: 78px;
		}
		&.hsn-complex {
			.hsn-to-dropdown-box {
				min-height: 143px;
			}
		}
	}
	.hsn-to-dropdown {
		display: block;
		width: 100%; 
		height: auto !important;
		margin: 0;
		border-bottom: 2px solid #ddd;
		border-radius: 0;
		font-size: 0;
		>.hsn-to-drop {
			display: inline-block;
			vertical-align: bottom;
			font-size: 14px;
			&.active,
			&.hsn-drop-active {
				.hsn-to-item {
					> a {
						&:after {
							background: @link-selected-color;
							.opacity(1);
							z-index: 12;
						}
					}
				}
			}
			.hsn-to-item {
				padding: 0 17px;
				text-transform: uppercase;
				> a {
					padding: 16px 0 10px;
					position: relative;
					color: @link-selected-color;
					font-weight: bold;
					.transition(color .3s);
					&:hover,
					&:active,
					&:focus {
						text-decoration: none;
					}
					&:active {
						color: @link-active-color;
					}
					&:after {
						content: "";
						position: absolute;
						width: 100%;
						height: 2px;
						bottom: -2px;
						left: 0;
						z-index: 2;
						.opacity(0);
						background-color: #666;
						.transition(all .3s);
					}
				}
			}
			.hsn-to-side {
				left: 0;
				top: auto;
				display: none;
				margin-top: 2px;
				z-index: -1;
				> ul {
					overflow: hidden;
					padding: 5px  0;
					> li {
						float: left;
						
						> a {
							color: #666;
							.transition(color .3s);
							padding: 4px 17px;
							&:hover,
							&:active,
							&:focus {
								background: transparent;
								outline: 0;
							}
							&:active {
								color: @link-active-color;
							}
							&:hover {
								color: @link-color;
							}
						}
						&.active {
							> a {
								color: @link-selected-color;
								&,
								&:hover {
									background: transparent;
								}
							}
						}
					}
				}

			}
			&:hover {
				.hsn-to-item {
					> a {
						color: @link-color;						
						&:after {
								background-color: @link-color;
								display: block;
								z-index: 12;
								.opacity(1);
						}
						
					}
				}
				.hsn-to-side {
					display: block;
					z-index: 10;
				}
			}
			&:active {
				.hsn-to-item {
					> a {
						color: @link-active-color;
						&:after {
								background: @link-active-color;
								.opacity(1);
						}
						
					}
				}
			}
		}
	}
	.hsn-s-results {
		background: transparent;
		.hsn-to-dropdown {
			border-bottom: 4px solid @blue-lighter-more;
			background: transparent;
			> .hsn-to-drop {
				&:first-child {
					.hsn-to-item {
						padding-left: 0;
					}
				}
				.hsn-to-item {
					> a {
						&:after {
							height: 4px;
							bottom: -4px;
							background-color: @blue-lighter-more;
						}
					}
				}
				&.hsn-drop-active {
					.hsn-to-item {
						> a {
							&:after {								
								background-color: @link-color;
							}
						}
					}
				}
			}
		}
	}
}
