//
// Tooltips
// --------------------------------------------------


// Base class
.tooltip {
  font-size: @font-size-xxxs;
  line-height: 14px;
}

// Wrapper for the tooltip content
.tooltip-inner {
  padding: 6px 8px;
  background-color: @tooltip-bg-fallback;
  background-color: @tooltip-bg;
	.box-shadow(0 2px 2px rgba(0,0,0,.2));
}
