// Mobile/tablet specific styles
@media (min-width: @screen-lg-min) {
	.gn-logo {
		margin-left: -9px;
	}
}
@media (max-width: @screen-sm-max) {
	.gn-app {
		display: none;
	}
	[class^="gn-col-"] {
		width: auto;
	}
	.gn-no-nav {
		.global-nav-oz { 
			height: 49px;
		}
	}
	.gn-container {
		> .sticky-header-active {
			position: static !important;			
			.box-shadow(none);
		}
		> .sticky-header-placeholder {
			display: none;
		}
		.gn-logo {
			a {
				background-image: url('../images/stx-staticlogo-drk-nopad-109x26.png');
			}
		}
		.home-page & {
			&.gn-post-sticky,
			.sticky-header-active {
				.global-nav-oz {
					background-color: transparent;
					.gn-icon {
						> .gn-icon-container {
							color: #fff;
						}
					}
				}
				.gn-side-opened {
					.gn-side-toggle {
						.gn-icon-container {
							color: #fff;
						}
					}
				}
			}
			.gn-logo {
				a {
					background-image: url('../images/stx-staticlogo-wht-nopad-109x26.png');
				}
			}
			.gn-user-opened {
				.gn-side-overlay {
					top: 0;
				}
			}
		}
	}
	.global-nav-oz {
		height: 49px;
		> .container {
			width: auto;
			padding-left: 12px;
			padding-right: 12px;
			> .row {
				margin-left: -12px;
				margin-right: -12px;
				> .col-md-12 {
					float: none;
					width: auto;
					.clearfix();
					//margin-left: 12px;
				}
			}
		}
	}
	.gn-logo {
		margin: 1px 0 2px -7px;
		a {
			width: 109px;
			height: 26px;
			line-height: 26px;
		}		
	}
	.gn-icon {
		height: 49px;
		margin: 0;
		> .gn-icon-container {
			outline: 0;
			padding: 15px 14px 11px;	
			> span {
				padding: 0;
			}
		}
	}
	.gn-cart {
		> .gn-icon-container {
			> i {
				top: 11px;
				right: -3px;
			}
		}
	}
	.gn-user {
		margin-right: 30px;
		position: static;
		> .gn-icon-container {
			width: 16px;
			height: 23px;
			.box-sizing(content-box);
			> i {
				display: none;
			}
			> span {
				position: absolute;
				padding: 15px 14px;
				top: 0;
				right: 0;
				bottom: 0;
				left: 0;
				height: 19px;
			}
		}
		&:hover,
		&.gn-user-pulled {
			.gn-user-drop-container {
				display: none;
			}		
		}
		
		.gn-user-drop {
			.box-sizing(border-box);
			padding: 10px 0;
			text-align: left;
			border-width: 1px 0 0 0;
			.box-shadow(0 2px 6px rgba(0,0,0,.4));
			&:before,
			&:after {
				right: 56px;
				top: -13px;
			}
			&:before {
				right: 55px;
			}
			> p,
			> a {
				display: block;
				font-size: 16px;
				line-height: 23px;
				white-space: normal;
				margin: 0 0 10px;
				padding: 0 12px;
			}
			> p {
				margin-right: 50px;
			}
			> hr {
				margin: 0 0 10px;
			}
			> [class^="ss-"] {
				font-size: 14px;
				line-height: 19px;
				width: 14px;
				height: 14px;
				color: #999;
				padding: 4px 14px;
				cursor: pointer;
				.box-sizing(content-box);
			}
			> [class^="fa fa-"] {
				font-size: 18px;
				line-height: 18px;
				width: 14px;
				height: 14px;
				color: #999;
				padding: 4px 14px;
				cursor: pointer;
				.box-sizing(content-box);
			}
		}
	}
	.gn-user-opened {
		.gn-user {
			.gn-user-drop-container {
				display: block;
				right: 0;
				left: 0;
				padding: 0;
			}
			.gn-icon-container {
				[class^="ss-"] {
					color: @link-color;
				}
				[class^="fa fa-"] {
					color: @link-color;
				}
			}
		}
		.gn-side-overlay {
			top: 49px;
			opacity: 1;
			visibility: visible;
		}
	}
	.gn-side-toggle {
		position: absolute;
		z-index: 3;
		right: 0;
		height: 49px;
		.transition(right .3s ease);
		margin: 0;
		padding: 0;
	}
	.gn-side-wrapper {
		position: absolute;
		overflow: hidden;
		//min-width: 100%;
		//min-height: 100%;
		right: 0;
		max-width: 0;
		background-color: #f8f8f8;
		.transition(max-width .3s ease);
		z-index: 3;
	}
	.gn-side-container {
		position: relative;
		width: 274px;
		top: 0;
		right: -274px;
		.transition(right 2s ease);
	}
	.gn-side-opened {
		.gn-side-toggle {
			right: 274px;
		}
		.gn-side-wrapper {
			max-width: 274px;
		}
		.gn-side-container {
			right: 0;
			.transition(right .1s ease);
		}
		.gn-side-toggle {
			.gn-icon-container {
				color: #fff;
			}
		}
	}
	.gn-search {
		margin: 0;
		padding: 10px;
		border-bottom: 1px solid #ccc;
		&.pull-left,
		&.pull-right {
			float: none !important;
		}
		form {
			> * {
				height: 32px;
			}
		}
		.gn-search-input { 
			width: 98%;
			width: 203px;
			padding: 8px 34px 8px 10px;
			font-size: 14px;
			line-height: 16px;
		}
		.gn-search-clear {
			top: 10px;
			right: 71px;
			font-size: 14px;
			line-height: 32px;
			> span {
				width: 14px;
			}
		}
		.gn-search-btn {
			width: 51px;
			line-height: 32px;
			position: absolute;
			right: 10px;
		}
	}
	.gn-menu {
		float: none;
		height: auto;
		padding: 10px;
		position: relative;
		left: 0;
		.transition(left .3s ease);
		.box-sizing(border-box);
		> ul {
			> li {
				float: none;
				background-color: #eee;
				margin-bottom: 1px;
				&.gn-menu-pulled {
					.gn-menu-item-1lvl {
						> a {
							color: @text-color;
						}
					}
				}
			}
		}
		&.gn-sub-menu-opened {
			left: -100%;
		}
	}
	.gn-menu-item-1lvl {
		background: #e3e3e3;
		position: relative;
		> a  {
			display: block;
			padding: 10px 50px 10px 10px;
			line-height: 20px;
			height: auto;
		}
		&:only-child {
			> span {
				background: #333;
			    left: auto;
    			width: 40px;
				> em {
				    margin-right: -5px;
				    background: transparent;
			        -webkit-transform: rotate(-45deg);
				    -moz-transform: rotate(-45deg);
				    -ms-transform: rotate(-45deg);
				    -o-transform: rotate(-45deg);
				    transform: rotate(-45deg);
					&:before {
						width: 14px;
					}
					&:after {
					    height: 14px;
					}
				}
			}
		}  
		> span {
			position: absolute;
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
			z-index: 3;
			cursor: pointer;
			background: rgba(227,227,227,0);
			> em {
				position: absolute;
				top: 0;
				bottom: 0;
				right: 0;
				width: 40px;
				background: @text-color;
				&:before,
				&:after {
					position: absolute;
					top: 50%;
					left: 7px;
					display: block;
					width: 26px;
					height: 2px;
					margin-top: -1px;
					background-color: #fff;
					content: "";
				}
				&:after {
					left: 19px;
					width: 2px;
					height: 26px;
					margin-top: -13px;
				}
			}
		}
	}
	.gn-dropdown {
		position: static;
		display: block;
		height: 0;
		overflow: hidden;
		.transition(height 0.35s ease);
		.box-shadow(none);
		> .container {
			width: auto;
			padding: 0;
			//padding: 0 10px;
		}
		.row{
			[class*="col"] {
				min-height: 0;
				display: block;
				float: none;
				width: auto;
				margin: 0;
			}
			
		}
	}
	.gn-dropdown-menu {
		min-height: 0;
		> [class*="col"] {
			height: auto!important;
		}
	}
	.gn-menu-opened {
		.gn-menu-item-1lvl {
			&:only-child {
			> span {
				> em {
			        background: transparent;
					&:after {
				        display: block;
					}
				}
			}
		}  
			> span {
				> em {
					background: @link-color;
					&:after {
						display: none;
					}
				}
			}
		}
		.gn-dropdown {
			height: auto!important;
		}
		
		.gn-dropdown-menu {
			max-height: 400px;
    		overflow-y: auto;
    		margin: 0;
    		> [class^="col-"] {
    			padding: 0;
    			> .row {
    				margin: 0;
    				> [class^="col-"] {
    					padding: 0;
    				}
    			}
    		}
		}
	}
	.gn-dropdown-menu {
		> [class^="gn-col-"] {
			> .gn-row {
				&,
				&:first-child {
					padding: 0;
				}
			}
		}
		> [class^="col-"] {
			> .row {
				&,
				&:first-child {
					padding: 0;
				}
			}
		}
	}
	.gn-menu-item-2lvl {
		position: relative;
		color: @link-color;
		cursor: pointer;
		> p {
			margin: 0;
			padding: 11px 40px 10px 10px;
		}
		> span {
			font-size: 12px;
			line-height: 12px;
			position: absolute;
			left: 0;
			right: 0;
			top: 0;
			bottom: 0;
			margin: 0;
			z-index: 2;
			background: rgba(238,238,238,0);
			&:before {
				position: absolute;
				top: 50%;
				right: 14px;
				margin-top: -4px;
				vertical-align: top;
				padding: 0;
			}
			&[class^="fa fa-"]{
			    font-size: 20px;
    			line-height: 10px;
			}
		}
		&:hover {
			> span {
				cursor: pointer;
			}
		}
	}
	.gn-menu-box-3lvl {
		position: absolute;
		right: -100%;
		top: 0;
		width: 100%;
		opacity: 0;
		padding: 10px;
		.box-sizing(border-box);
		z-index: 1;
		&.gn-sub-menu-selected {
			opacity: 1;
			z-index: 2;
		}
		> a {
			padding: 13px 10px 12px;
			font-size: 12px;
			line-height: 15px;
			font-weight: bold;
			text-transform: uppercase;
			background: #e3e3e3;
			display: block;
			> span {
				font-size: 12px;
				line-height: 12px;
				width: 12px;
				height: 12px;
				vertical-align: middle;
				&[class^="fa fa-"]{
				    font-size: 20px;
	    			line-height: 10px;
				}
			}
			&:hover {
				text-decoration: none;
			}
			&:active {
				color: @link-color;
			}
		}
		> p {
			font-size: 16px;
			line-height: 23px;
			padding: 15px 10px 0;
			margin: 0;
			background: #eee;
		}
		ul {
			padding: 3px 10px 10px;
			background: #eee;
			> li {
				font-size: 14px;
				line-height: 17px;
				padding: 10px 0;
			}
		}
	}
	.gn-store {
		margin: 0;
		width: auto;
		padding: 10px;
		border-top: 1px solid #ccc;
		&.pull-left,
		&.pull-right {
			float: none!important;
		}
		> a {
			height: auto;
			padding: 10px 22px 9px 22px;
			font-size: 14px;
			line-height: 18px;
		}
	}
	.gn-promo {
		display: none;
	}
	.gn-container {
		.global-nav-oz {
			.gn-menu-item-1lvl {
				> a {
					color: @text-color;
				}
			}
		}
	}
	
	.gn-logo .seagate-portal-logo {
		display: inline-block;
		top: 18px;
		font-size: 13px;
		left: 100px;
		color: #fff;
	}
}

@media (max-width: @screen-xs-max) {
	.gn-logo {
		margin-left: -6px;
	}
}
@media (max-width: 479px) {
	.gn-no-nav {
		.gn-logo {
			a {
				width: 30px;
			}
		}
	}
}
@media (min-width: @screen-md-min) {
	.gn-side-wrapper {
		height: auto!important;
	}
	.gn-user-drop {
		min-height: 0!important;
	}
	.gn-dropdown {
		height: auto!important;
	}
}