.pre-search-filtering-size(@input-height; @margin-filter-display; @padding-horizontal-filter-display; @font-size; @padding-filter-drop; @line-height) {
	.sf-psf {
		height: @input-height;
		padding: @margin-filter-display;
		font-size: @font-size;
		&.sf-psf-inline {
			@media (min-width: @screen-md-min) {
				padding-right: (@margin-filter-display * 2);
			}
		}
	}
	.sf-psf-display {
		padding: 0 @padding-horizontal-filter-display;
		line-height: (@input-height - @margin-filter-display * 2);
		height: (@input-height - @margin-filter-display * 2);
		cursor: pointer;
		> [class^="ss-"] {
			font-size: (@font-size - 1);
		}
		> [class^="fa fa-"] {
			
		}
	}
	.sf-psf-drop {
		right: (@margin-filter-display + @padding-horizontal-filter-display);
		padding-top: @margin-filter-display;
	}
	.sf-psf-drop-container {
		padding: @padding-filter-drop;
	}
	.sf-psf-list {
		font-size: @font-size;
		line-height: (@line-height - 1);
		> li {
			padding-left: (@font-size + 16);
			> [class^="ss-"] {
				margin-top: (-((@line-height - 1) / 2));
			}	
			> [class^="fa fa-"] {
				margin-top: (-((@line-height - 1) / 2));
			}	
		}
	}
}