// Repeated Search Field (CORE)
.basic-search-results {
	font-size: @font-size-small;
	line-height: (@font-size-small + 5);
}
.bsr-header {
	margin-top: 1em;
	font-weight: bold;
}
.bsr-container {
	min-height: 60px;
	margin-bottom: 20px;
	.media-left {
		.btn {
			display: block;
			margin-top: 8px;
		}
	}
	.media-body {
		> p {
			margin-bottom: 0;
			&.media-desc {
				margin-bottom: 3px;
			}
		}
	}
	.media-heading {
		font-size: @font-size-small;
		line-height: (@font-size-small + 5);
		margin-bottom: 0;
	}
	.media-url,
	.media-link {
		font-size: @font-size-xxs;
		line-height: (@font-size-xxs + 3);
		color: #999;
		padding: 2px 0;
	}
	.media-url {
		> a {
			color: #999;
		}
	}
}

.bsr-footer {
	.clearfix();
	font-size: @font-size-xxs;
	line-height: (@font-size-xxs + 3);
	margin-bottom: 1em;
}