.vertical-sub-navigation {
	overflow: hidden;
	background: #f8f8f8;
	.box-shadow(0 1px 3px rgba(0,0,0,.25));
	font-size: @font-size-small;
	line-height: @line-height-small-computed;
	.vsn-menu {
		> ul {
			margin: 0;
			padding: 0;
			> li {
				list-style: none;
				> ul {
					margin: 0;
					padding: 0;
					> li {
						list-style: none;
					}
				}
				> .vsn-dropdown {
					> ul {
						margin: 0;
						padding: 0;
						> li {
							list-style: none;
							> .vsn-menu-item-lv3 {
								> ul {
									margin: 0;
									padding: 0;
									> li {
										list-style: none;
									}
								}
							}	
						}
					}
				}
			}
		}
	}
	&.simple {
		> .vsn-menu {
			position: relative;
			padding-bottom: 5px;
			> ul {
				> li {
					&:first-child {
						> .vsn-menu-item-header {
							&:before {
								display: none;
							}
						}
					}
					> .vsn-menu-item-header {
						color: @text-color;
						font-weight: bold;
						padding: 10px 15px 5px;
						text-transform: uppercase;
						display: block;
						margin-bottom: 0; 
						
						&:hover,
						&:active,
						&:focus {
							text-decoration: none;
						}
						&:before {
							content: "";
						  	display: block;
						  	width: 100%;
						  	height: 1px;
						  	background: #CFCFCF;
						  	margin: 0 0 10px 0;
						}
					}
					> ul {
						> li {
							> a {
								padding: 5px 15px;
								display: block;
								&:hover,
								&:active,
								&:focus {
									background: #ddd;
									text-decoration: none;
								}
								&:hover {
									color: @link-hover-color;
								}
								&:active,
								&:focus {
									color: @link-active-color;
								}
								&.active {
									background: @link-selected-color;
									color: @white-base;
								}
							}
						}
					}
				}
			}
		}
	}
	&.accordion {	
		> .vsn-menu {
			height: auto;
			padding: 10px;
			position: relative;
			left: 0;
			.transition(left .3s ease);
		  	> ul {	
		  		> li {
		  			background-color: #eee;
	  				margin-bottom: 1px;
		  			.vsn-menu-item-lv1 {
	  				  background: #e3e3e3;
							position: relative;
		  				> a {
		  				  display: block;
								font-size: 12px;
								font-weight: bold;
							  padding: 10px 50px 10px 10px;
							  line-height: 20px;
							  height: auto;
							  color: @text-color;
							  text-transform: uppercase;
		  				}
		  				> span {
	  						position: absolute;
						  	top: 0;
						  	right: 0;
						  	bottom: 0;
						  	left: 0;
						  	z-index: 3;
						  	cursor: pointer;
						  	background: rgba(227,227,227,0);
		  					> em {
	  							position: absolute;
							  	top: 0;
							  	bottom: 0;
							  	right: 0;
							  	width: 40px;
							  	background: #333;
							  	&:before,
							  	&:after {
						  		  	position: absolute;
								  	top: 50%;
								  	left: 7px;
								  	display: block;
								  	width: 26px;
								  	height: 2px;
								  	margin-top: -1px;
								  	background-color: @white-base;
								  	content: "";
							  	}
							  	&:after {
						  		  	left: 19px;
								  	width: 2px;
								  	height: 26px;
								  	margin-top: -13px;
							  	}
		  					}
		  				}
		  			}
		  			.vsn-dropdown {
		  				display: none;
		  				> ul {
		  					&.vsn-dropdown-menu {
		  						> li {
		  							&.vsn-menu-item-lv2 {
		  								position: relative;
		  								&:hover {
		  									> a {
		  										background: #ddd;
		  										color: @link-hover-color;
		  									}
		  									
		  									> span {
		  										color: @link-hover-color;
		  									}
		  								}
		  								&:active,
		  								&:focus {
		  									> a {
		  										background: #ddd;
		  										color: @link-active-color;
		  									}
		  									
		  									> span {
		  										color: @link-active-color;
		  									}
		  								}
		  								> a {
	  									    padding: 10px 50px 10px 10px;
	  										display: block;
		  								}
		  								> span {
		  									  position: absolute;
											  cursor: pointer;
											  display: block;
											  left: 0;
											  right: 0;
											  bottom: 0;
											  top: 0;
											  &[class^="ss-"] {
										  		&:before {
												    right: 10px;
												    margin-top: -7px;
												  }
											  }
											  &[class^="fa fa-"] {
											  	font-size: 24px;
											  	&:before {
												    right: 15px;
												    margin-top: -12px;
												  }
											  }
											  &:before {
											  	position: absolute;
											    top: 50%;
											  }
		  								}
		  								.vsn-menu-item-lv3 {
		  									position: absolute;
		  									left: 100%;
		  									top: 0;
		  									width: 100%;
		  									padding: 10px;
		  									
		  									display: none;
		  									> a {
		  										display: block;
		  										padding: 10px;
	  											background: #e3e3e3;
	  											color: @link-color;
	  											cursor: pointer;
	  											text-transform: uppercase;
	  											.fa-caret-left {
	  												font-size: 24px;
	  												vertical-align: middle;
	  											}
	  											&:hover {
	  												text-decoration: none;
	  											}
		  									}
		  									> p {
		  										  padding: 10px;
		  										  margin: 0;
		  										  background: #eee;
														font-size: @font-size-base;
														line-height: @line-height-base;
		  									}
		  									> ul {
		  										&.vsn-side-menu {
		  											background: #eee;
		  											> li {
		  												> a {
		  													padding: 10px;
		  													display: block;
		  													&:hover {
		  														background: #ddd;
																color: @link-hover-color;
															}
															&:active,
															&:focus {
																background: #ddd;
																color: @link-active-color;
															}
		  												}
		  											}
		  										}
		  									}
		  								}
		  							}
		  						}
		  					}
		  				}
		  			}
		  		}
		  	}
	  	}
	  	&.vsn-sided {
	  		.vsn-menu {
	  			left: -100%;
	  			.transition(left .3s ease);
	  			> ul {
	  				> li {
	  					.vsn-dropdown {
	  						> ul {
	  							&.vsn-dropdown-menu {
			  						> li {
			  							&.vsn-menu-item-lv2 {
			  								&.vsn-menu-sided {
			  									position: static;
				  								.vsn-menu-item-lv3 {
				  									display: block;
				  								}
			  								}
			  							}
			  							
			  						}
			  					}
	  						}
	  					}
	  				}
	  			}
	  		}
	  	}
		&.vsn-opened {
			.vsn-menu {
				> ul {
					> li {
				  		&.vsn-menu-pulled {
				  			.vsn-menu-item-lv1 {
				  				> span {
				  					> em {
			  							background: @link-color;
									  	&:after {
								  		  	display: none;
									  	}
				  					}
				  				}
				  			}
				  			.vsn-dropdown {
				  				display: block;
				  				> ul {
				  					> li {
				  						&.vsn-menu-item-lv2 {
				  							&.active {
			  									> a {
			  										background: @link-selected-color; 
			  										color: @white-base;
			  									}
			  									> span {
			  										color: @white-base; 
			  									}
		  									}
				  						}
				  					}
				  				}
				  			}
				  		}
					}
				}
			}
		}
	}
}

