.arrow-up-outline(@auo-fill: #fff; @auo-stroke: #333) {
	&:before,
	&:after {
		position: absolute;
		top: -9px;
		right: 0;
		bottom: 100%;
		width: 0;
		height: 0;
		pointer-events: none;
		border: solid transparent;
		content: " ";
	}
	&:after {
		right: 1px;
		margin-left: -8px;
		border-color: transparent;
		border-bottom-color: @auo-fill;
		border-width: 7px;
	}
	&:before {
		margin-top: -2px;
		margin-left: -16px;
		border-color: transparent;
		border-bottom-color: @auo-stroke;
		border-width: 8px;
	}
}