//
// Typography
// --------------------------------------------------


// Headings
// -------------------------


h1, .h1,
h2, .h2,
h3, .h3 {
  small,
  .small {
    font-size: 85%;
  }
}
h4, .h4,
h5, .h5,
h6, .h6 {
  small,
  .small {
    font-size: 85%;
  }
}

.headline-jumbo { 
	font-size: (@font-size-h-jumbo - 27);
	line-height: (@font-size-h-jumbo - 27 + 4);
	@media (min-width: @screen-md-min) {
		font-size: @font-size-h-jumbo;
		line-height: (@font-size-h-jumbo - 3);
  }
}
.headline-big { 
	font-size: (@font-size-h-big - 17);
	line-height: (@font-size-h-big - 17 + 2);
	@media (min-width: @screen-md-min) {
		font-size: @font-size-h-big;
		line-height: (@font-size-h-big + 4);
  }
}
h1, .h1 { 
	font-size: (@font-size-h1 - 15);
	line-height: (@font-size-h1 - 15 + 4);
	@media (min-width: @screen-md-min) {
		font-size: @font-size-h1;
		line-height: (@font-size-h1 + 6);
  }
}
h2, .h2 { 
	font-size: (@font-size-h2 - 8);
	line-height: (@font-size-h2 - 8 + 4);
	@media (min-width: @screen-md-min) {
		font-size: @font-size-h2;
		line-height: (@font-size-h2 + 4);
  }
}
h3, .h3 { 
	font-size: (@font-size-h3 - 8); 
	line-height: (@font-size-h3 - 8 + 4); 
	@media (min-width: @screen-md-min) {
		font-size: @font-size-h3;
		line-height: (@font-size-h3 + 4); 
  }
}
h4, .h4 { 
	font-size: (@font-size-h4 - 3); 
	line-height: (@font-size-h4 - 3 + 5); 
	@media (min-width: @screen-md-min) {
		font-size: @font-size-h4;
		line-height: (@font-size-h4 + 4); 
  }
}
h5, .h5 { 
	font-size: (@font-size-h5 - 3); 
	line-height: (@font-size-h5 - 3 + 5);
	@media (min-width: @screen-md-min) {
		font-size: @font-size-h5;
		line-height: (@font-size-h5 + 4);
  }
}
h6, .h6 { 
	font-size: (@font-size-h6 - 3);
	font-weight: 400;
	line-height: (@font-size-h6 - 3 + 7);
	@media (min-width: @screen-md-min) {
		font-size: @font-size-h6;
		line-height: (@font-size-h6 + 5);
  }
}

// Body text
// -------------------------

p {
  margin: 0 0 (@line-height-small-computed / 2);
	@media (min-width: @screen-md-min) {
		margin-bottom: (@line-height-computed / 2);		
  }
}

.body-small {
	font-size: @font-size-small;
	line-height: @line-height-small;
  margin-bottom: (@line-height-small-computed / 2);
}

.descriptor-text {
	font-size: @font-size-xxs;
	line-height: (@font-size-xxs + 5);
  margin-bottom: ((@font-size-xxs + 5) / 2);
	
	@media (min-width: @screen-sm-min) {
    font-size: @font-size-xs;
		line-height: (@font-size-xs + 5);
		margin-bottom: ((@font-size-xs + 5) / 2);
  }
}

.smallcap-text {
	font-size: @font-size-xxs;
	line-height: (@font-size-xxs + 3);
  margin-bottom: ((@font-size-xxs + 3) / 2);
	text-transform: uppercase;
	font-weight: bold;
}

.timestamp-text {
	font-size: @font-size-xxxs;
	line-height: (@font-size-xxxs + 3);
  margin-bottom: ((@font-size-xxxs + 3) / 2);
	color: #999;
}

.footer-text {
	font-size: @font-size-xxxs;
	line-height: @font-size-xxxs;
  margin-bottom: (@font-size-xxxs / 2);
	color: #999;
}

// Emphasis & misc
// -------------------------

// Ex: (12px small font / 14px base font) * 100% = about 85%
small,
.small {
  font-size: floor((100% * @font-size-small / @font-size-base));
}

mark,
.mark {
  background-color: @state-warning-bg;
  padding: .2em;
}

// Alignment
.text-left           { text-align: left; }
.text-right          { text-align: right; }
.text-center         { text-align: center; }
.text-justify        { text-align: justify; }
.text-nowrap         { white-space: nowrap; }

// Transformation
.text-lowercase      { text-transform: lowercase; }
.text-uppercase      { text-transform: uppercase; }
.text-capitalize     { text-transform: capitalize; }

// Contextual colors
.text-alt-normal {
  color: @state-alt-normal-text;
}
.text-hint {
  color: @state-hint-text;
}
.text-ghost {
  color: @state-ghost-text;
}
.text-danger {
	color: @state-danger-text;
}
.text-success {
	color: @state-success-text;
}
.text-warning {
	color: @state-warning-text;
}
.text-info {
	color: @state-info-text;
}

// Contextual backgrounds
// For now we'll leave these alongside the text classes until v4 when we can
// safely shift things around (per SemVer rules).
.bg-warning,
.bg-danger,
.bg-success,
.bg-info {
	padding: @alert-padding;
	border-width: 1px;
	border-style: solid;
	border-radius: @border-radius-base;
	.bg-cont-link {
		color: inherit;
	}
}
.bg-warning {
	color: @state-warning-text;
  background-color: @state-warning-bg;
	border-color: @state-warning-border;
}
.bg-danger {
	color: @state-danger-text;
  background-color: @state-danger-bg;
	border-color: @state-danger-border;
}
.bg-success {
	color: @state-success-text;
	background-color: @state-success-bg;
	border-color: @state-success-border;
}
.bg-info {
	color: @state-info-text;
  background-color: @state-info-bg;
	border-color: @state-info-border;
}

// Page header
// -------------------------

.page-header {
  padding-bottom: ((@line-height-computed / 2) - 1);
  margin: (@line-height-computed * 2) 0 @line-height-computed;
  border-bottom: 1px solid @page-header-border-color;
}


// Lists
// -------------------------

// Unordered and Ordered lists
ul,
ol {
  margin-top: 0;
  margin-bottom: (@line-height-computed / 2);
  ul,
  ol {
    margin-bottom: 0;
  }
}

// List options

// Unstyled keeps list items block level, just removes default browser padding and list-style
.list-unstyled {
  padding-left: 0;
  list-style: none;
}

// Inline turns list items into inline-block
.list-inline {
  .list-unstyled();
  margin-left: -5px;

  > li {
    display: inline-block;
    padding-left: 5px;
    padding-right: 5px;
  }
}

// Description Lists
dl {
  margin-top: 0; // Remove browser default
  margin-bottom: @line-height-computed;
}
dt,
dd {
  line-height: @line-height-base;
}
dt {
  font-weight: bold;
}
dd {
  margin-left: 0; // Undo browser default
}

// Horizontal description lists
//
// Defaults to being stacked without any of the below styles applied, until the
// grid breakpoint is reached (default of ~768px).

.dl-horizontal {
  dd {
    &:extend(.clearfix all); // Clear the floated `dt` if an empty `dd` is present
  }

  @media (min-width: @grid-float-breakpoint) {
    dt {
      float: left;
      width: (@dl-horizontal-offset - 20);
      clear: left;
      text-align: right;
      .text-overflow();
    }
    dd {
      margin-left: @dl-horizontal-offset;
    }
  }
}


// Misc
// -------------------------

// Abbreviations and acronyms
abbr[title],
// Add data-* attribute to help out our tooltip plugin, per https://github.com/twbs/bootstrap/issues/5257
abbr[data-original-title] {
  cursor: help;
  border-bottom: 1px dotted @abbr-border-color;
}
.initialism {
  font-size: 90%;
  .text-uppercase();
}

// Blockquotes
blockquote {
  padding: (@line-height-computed / 2) @line-height-computed;
  margin: 0 0 @line-height-computed;
  font-size: @blockquote-font-size;
  border-left: 5px solid @blockquote-border-color;

  p,
  ul,
  ol {
    &:last-child {
      margin-bottom: 0;
    }
  }

  // Note: Deprecated small and .small as of v3.1.0
  // Context: https://github.com/twbs/bootstrap/issues/11660
  footer,
  small,
  .small {
    display: block;
    font-size: 80%; // back to default font-size
    line-height: @line-height-base;
    color: @blockquote-small-color;

    &:before {
      content: '\2014 \00A0'; // em dash, nbsp
    }
  }
}

// Opposite alignment of blockquote
//
// Heads up: `blockquote.pull-right` has been deprecated as of v3.1.0.
.blockquote-reverse,
blockquote.pull-right {
  padding-right: 15px;
  padding-left: 0;
  border-right: 5px solid @blockquote-border-color;
  border-left: 0;
  text-align: right;

  // Account for citation
  footer,
  small,
  .small {
    &:before { content: ''; }
    &:after {
      content: '\00A0 \2014'; // nbsp, em dash
    }
  }
}

// Addresses
address {
  margin-bottom: @line-height-computed;
  font-style: normal;
  line-height: @line-height-base;
}
