// Pre-Search Filtering

.pre-search-filtering {
	.sf-input-container {
		position: relative;
	}
	.sf-btn-clear {
		right: 4px;
	}
}
.sf-psf {
	position: relative;
	float: right;
	height: @input-height-base;
	padding: @margin-filter-display-md;
	font-size: @font-size-small;	
	&:hover {
		.sf-psf-drop {
			display: block;
		}
	}
	&.sf-psf-inline {
		padding-right: (@margin-filter-display-md * 2);
		.radio {
			margin: 0;
			padding: 4px 6px;
		}		
		@media (min-width: @screen-md-min) {
			.sf-psf-display {
				display: none;
			}
			.sf-psf-drop {
				display: block;
				position: static;
				padding: 0;
			}
			.sf-psf-drop-container {
				border: 0;
				padding: 0;
				background: none;
				&:before,
				&:after {
					content: none;
				}
			}
			.radio {
				display: inline-block;
				vertical-align: middle;
				font-weight: normal;
				padding-left: 15px;
				padding-top: (((@input-height-base - @margin-filter-display-md * 2) - 23) / 2);
				&:first-child {
					padding-left: 0;
				}
				label {
					margin: 0;
					font-weight: normal;
				}
				& + .radio {
					margin: 0;
				}
			}
		}
	}
}
.sf-psf-display {
	background: #ddd;
	border-radius: 3px;
	padding: 0 @padding-horizontal-filter-display-md;
	line-height: (@input-height-base - @margin-filter-display-md * 2);
	height: (@input-height-base - @margin-filter-display-md * 2);
	cursor: pointer;
	> [class^="ss-"] {
		color: @link-color;
	}
	> [class^="fa fa-"] {
		color: @link-color;
		font-size: 1.4em;
	}
}
.sf-psf-drop {
	display: none;
	position: absolute;
	right: (@margin-filter-display-md + @padding-horizontal-filter-display-md);
	padding-top: @margin-filter-display-md;
	z-index: 100;
}
.sf-psf-drop-container {
	.arrow-up-outline();
	background: #fff;
	border: 1px solid #333;
	padding: @padding-filter-drop-md;
	white-space: nowrap;
}
.sf-psf-list {
	list-style: none;
	margin: 0;
	padding: 0;
	font-size: @font-size-small;
	line-height: @line-height-small-computed;
	text-align: right;
	> li {
		position: relative;
		cursor: default;
		padding: 4px 6px 4px (@font-size-small + 16);
		&:hover {
			background: @link-hover-bg;
		}
		&:active {
			background: @link-active-color;
			color: #fff;
		}
		&.selected {
			background: #29373D;
			color: #fff;
			> [class^="ss-"] {
				display: block;
			}	
			> [class^="fa fa-"] {
				display: block;
			}			
		}
		> [class^="ss-"] {
			display: none;
			position: absolute;
			left: 8px;
			top: 50%;
			margin-top: (-(@line-height-small-computed / 2));
			color: #fff;
		}
		> [class^="fa fa-"] {
			display: none;
			position: absolute;
			left: 8px;
			top: 50%;
			margin-top: (-(@line-height-small-computed / 2));
			color: #fff;
		}		
	}
}

.sf-xs {
	.pre-search-filtering-size(@input-height-xs; @margin-filter-display-xs; @padding-horizontal-filter-display-xs; @font-size-xs; @padding-filter-drop-xs; @line-height-small-computed);
	.sf-psf-drop-container {
		&:before,
		&:after {
			top: -10px;
		}
	}
	.sf-psf-inline {
		.radio {
			padding-top: 0;
		}
	}
	.radio-custom + label {
		> span {
			@media (min-width: @screen-md-min) {
				top: 2px;
			}
		}
	}
}
.sf-sm {
	.pre-search-filtering-size(@input-height-small; @margin-filter-display-sm; @padding-horizontal-filter-display-sm; @font-size-small; @padding-filter-drop-sm; @line-height-small-computed);
	.sf-psf-inline {
		.radio {
			padding-top: (((@input-height-small - @margin-filter-display-sm * 2) - 23) / 2);
		}
	}
}
.sf-lg {
	.pre-search-filtering-size(@input-height-large; @margin-filter-display-lg; @padding-horizontal-filter-display-lg; @font-size-base; @padding-filter-drop-lg; @line-height-computed);
	.sf-psf-drop-container {
		&:before,
		&:after {
			top: -8px;
		}
	}
	.sf-psf-inline {
		.radio {
			padding-top: (((@input-height-large - @margin-filter-display-lg * 2) - 23) / 2);
		}
	}
}


