.fat-footer-wrapper {
	background-color: #f8f8f8;
	padding: 10px 0;
	color: #999;
	position: relative;
	margin-top: 100px;
	.fat-footer-directory {
		overflow: hidden;
		padding: 10px 0;
		border-bottom: 1px solid #e8e8e8;
		.ff-column {
			float: left;
			width: 20%;
			padding-right: 10px;
			&.ff-multi-columns {
				width: 40%;
				> ul {
					float: left;
					width: 50%;
					vertical-align: top;
				}
			}
			&.ff-sns-column {
				.sns-links {
					overflow: hidden;
					> li {
						width: 33.33%;
						float: left;
						margin-right: 0;
					}
				}
			}
			.ff-title {
				font-size: 16px;
				line-height: 23px;
				margin-bottom: 7px;
				display: block;
			}
			> ul {
				margin: 0;
				padding: 0;
				> li {
					list-style: none;
					font-size: 12px;
					line-height: 15px;
					padding: 3px 0 2px;
				}
			}
		}
		+ .fat-footer-links {
			text-align: left;
		}
	}
	.fat-footer-links {
		padding: 10px 0;
		text-align: center;
		> ul {
			padding: 0;
			margin: 0;
			> li {
				font-size: 12px;
				line-height: 15px;
				padding: 3px 16px 0 2px;
				display: inline-block;
				margin-right: 2px;
				list-style: none;
				&:before {
					content:"";
					display: inline-block;
					width: 4px;
					height: 4px;
					background: #333;
				  	border-radius: 100%;
				  	vertical-align: middle;
				  	margin-right: 2px;
				  	display: none;
				}

				&:first-child {
					&:before {
						display: none;
					}
					margin-left: 0;
				}
				> a {
					&.globe-link {
						> i {
							&[class^="ss-"]{
								font-size: 9px;
							}
							&[class^="fa fa-"] {
								font-size: 16px;
							}
						}
					}
				}
			}
		}
	}
}

@media(max-width: @screen-xs-max) {
	.fat-footer-wrapper {
		.fat-footer-directory {
			border-top: 0;
			.ff-column {
				float: none;
				width: 100%;
				margin-bottom: 10px;
				padding-right: 0;
				&.ff-multi-columns {
					width: 100%;
					> ul {
						float: none;
						width: 100%;
					}
				}
			}
		}
		.fat-footer-links {
			border-bottom: none;
			> ul {
				> li {
					display: block;
					&:before {
						display: none;
					}
				}
			}
		}
	}
}