.drawer-overlay {
	&.dark {
		.drawer-toggle {
			background: #3e4d52;
			color: @white-base;
			.drawer-toggle-icon {
				&:before {
				  background: #2f3a3e;
			 	}
			}
		}
	}
	&.gray {
		.drawer-toggle {
			background: #666;
			color: @white-base;
			.drawer-toggle-icon {
				&:before {
				  background: #555;
			 	}
			}
		}
	}
	&.green {
		.drawer-toggle {
			background: #548738;
			color: @white-base;
			.drawer-toggle-icon {
				&:before {
				  background: #385E24;
				}
			}
		}
	}
	.drawer-toggle {
		background: #007a9e;
		color: @white-base;
		.drawer-toggle-icon {
			&:before {
				  background: #005C7B;
			 	}
		}
		.drawer-toggle-text{
			
		}
	}
	.drawer-box {
		width: 400px;
		background: #f0f0f0;
		min-height: 240px;
		
		.drawer-title {
			padding: 10px;
			background: #999;
			color: @white-base;
			p { 
				margin: 0;
				padding-right: 30px;
			}
			
			.drawer-close {
				position: absolute;
			  	right: 10px;
			  	top: 10px;
			  	display: block;
			  	cursor: pointer;
			}
		}
		.drawer-content {
			padding: 10px;
			overflow: auto;
		}
	}
	&.feedback-side-drawer {
		position: fixed;	
		right: 0;
		left: 100%;
		margin-left: 0;
		.transition(margin-left .3s);
		
		&.opened {
			.transition(margin-left .3s);
			.drawer-toggle {
				.drawer-toggle-icon {
					i {
						&.ss-navigateleft {
							display: none;
						}
						&.ss-navigateright {
							display: block;
						}
						&.fa-chevron-left {
							display: none;
						}
						&.fa-chevron-right {
							display: block;
						}
					}
				}
			}
		}	
		.drawer-toggle {
			position: absolute;
			height: 240px;
			width: 40px;
			left: -40px;
			text-align: center;
			border-radius: 3px 0 0 3px;
			&:hover,
			&:active,
			&:focus {
				text-decoration: none;
			}
			.drawer-toggle-icon {
				padding-top: 15px;
				display: block;
				position: relative;
				z-index: 10;
				&:before {
				  content: "";
				  border-radius: 100%;
				  display: block;
				  position: absolute;
				  width: 26px;
				  height: 26px;
				  left: 50%;
				  margin-left: -13px;
				  top: 9px;
				  z-index: -1;	  
				}
				i {
					&.ss-navigateleft {
						display: block;
					}
					&.ss-navigateright {
						display: none;
					}
					&.fa-chevron-left {
						display: block;
					}
					&.fa-chevron-right {
						display: none;
					}
				}
			}
			.drawer-toggle-text{
				.rotate(-90deg);
				display: block;
				width: 140px;
				left: -50px;
				position: absolute;
				top: 100px;
				font-size: 12px;
				font-weight: bold;
			}
		}
	}
	
	&.chat-bottom-drawer {
		position: fixed;	
		bottom: 0;
		top: 100%;
		margin-top: 0;
		.transition(margin-top .3s);
		&.opened {
			bottom: 0;
			.transition(margin-top .3s);
			.drawer-toggle {
				.drawer-toggle-icon {
					i {
						&.ss-navigateup {
							display: none;
						}
						&.ss-navigatedown {
							display: block;
						}
						&.fa-chevron-up {
							display: none;
						}
						&.fa-chevron-down {
							display: block;
						}
					}
				}
			}
		}	
		.drawer-toggle {
			position: absolute;
			border-radius: 3px 3px 0 0;
			padding: 8px 50px 7px 8px;
			width: 210px;
			&:hover,
			&:active,
			&:focus {
				text-decoration: none;
			}
			.drawer-toggle-icon {
				position: absolute;
				right: 8px;
				top: 8px;
				border-radius: 50%;
				background: #005C7B;
				width: 24px;
				height: 24px;
				text-align: center;
				&:before {
				  //content: "";
				  border-radius: 100%;
				  display: block;
				  position: absolute;
				  width: 26px;
				  height: 26px;
				  left: 50%;
				  margin-left: -8px;
				  top: -5px;
				  z-index: -1;	  
				}
				i {
					&.ss-navigateup {
						line-height: 24px;
						display: block;
					}
					&.ss-navigatedown {
						display: none;
						line-height: 28px;
					}
					&.fa-chevron-up {
						line-height: 24px;
						display: block;
					}
					&.fa-chevron-down {
						display: none;
						line-height: 28px;
					}
				}
			}
			.drawer-toggle-text{
			  	display: inline-block;
  				max-width: 300px;
					font-size: 12px;
					font-weight: bold;
			}
		}
		.drawer-box {
		}
	}
}

.modal-overlay {
	.modal-header {
		position: relative;
		text-align: center;
		.modal-back {
			position: absolute;
			top: 50%;
			margin-top: -9px;
			left: 15px;
			&:hover,
			&:active,
			&:focus {
				text-decoration: none;
			}
			
			i {
				vertical-align: middle;
				margin-right: 5px;
			}
		}
	}
	.close {
		  position: absolute;
		  right: 15px;
		  top: 50%;
		  margin-top: -12px;
	}
	.modal-title {
		padding: 0 20px 0 50px;
	}
	.modal-body {
		overflow-x: hidden;
		overflow-y: hidden;
		min-height: 100px;
	}
	.modal-btn-left {
		float: left;
	}
	.modal-btn-right {
		float: right;
	}
	.modal-back {
		display: none;
	}
	.modal-footer {
		.btn {
			margin-right: 5px;
			margin-left: 5px;
		}
	}
	&.simple-input-overlay {
		  .modal-body {
			overflow-y: auto;
		}
	}
	
	&.input-wizard-overlay {
		.modal {
			.modal-finish {
				display: none;
			}
			.modal-body {
				.transition(height .3s);
				padding: 15px 0;
				position: relative;
				.modal-inner {
					position: absolute;
					width: 100%;
					left: 0;
					top: 15px;
					.transition(left .3s);
					.modal-step {
						position: absolute;
						width: 100%;
						padding: 0 15px;
						&.modal-step-1 {
							right: 0;
						}
						&.modal-step-2 {
							right: -100%;
						}
						&.modal-step-3 {
							right: -200%;
						}
					}
				}
			}
			&.opened-2 {
				.modal-body {
					.transition(height .3s);
					.modal-inner {
						left: -100%;
						.transition(left .3s);
					}					
				}
				.modal-back {
					display: block;
				}
			}
			&.opened-3 {
				.modal-body {
					.transition(height .3s);
					.modal-inner {
						left: -200%;
						.transition(left .3s);
					}					
				}
				.modal-back {
					display: block;
				}
				.modal-next {
					display: none;
				}
				.modal-finish {
					display: block;
				}
			}
		}
	}
	
	&.media-viewer-overlay { 
		.modal-title {
			padding-left: 0;
			text-align: left;
		}
		.m-carousel {
			&.m-carousel-horizontal {
				.m-carousel-inner {
					.m-item {
						width: 100%;
					}
				}
			}
		}  
	}
}

@media(max-width: @screen-xs-max){
	.drawer-overlay {
		&.chat-bottom-drawer { 
			left: 2px;
			right: 2px;
			.drawer-toggle {
				width: 100% !important;
			}
			.drawer-box {
				width: 100%;
			}
		}	
		&.feedback-side-drawer {	
			
				width: 100%;
				padding-left: 40px;
				margin-left: -40px!important;
				.drawer-toggle {
					left: 0;
				}
				.drawer-box {
					width: 100%;
				}
				&.opened {
					margin-left: -100%!important;
				}
			}
		
	}
	
	.modal-overlay {
		.modal-footer {
			button {
				&.btn {
					width: 100%;
					float: none;
					margin: 0 0 10px 0;
					display: block;
				}
			}
		}
	}
	
}


