//
// Breadcrumbs
// --------------------------------------------------


.breadcrumb {
	float: left;
	clear: both;
  background-color: #fff;
	font-size: 0;
	line-height: 0;
	padding: 3px 0 3px 6px; 
	margin: 8px 0 8px -6px;
  > .active {
    color: @breadcrumb-active-color;
		font-weight: bold;
		+ li:before {      
			font-weight: normal;
    }
  }
	> li {
		font-size: 11px;
		line-height: 14px;
		height: 14px;
		margin-right: 6px;
		&:before {
			content: none;
			display: none;
		}
		> .ss-icon {
			font-size: 7px;
			line-height: 14px;
		}
		> [class^="fa fa-"] {
			font-size: 8px;
		}
	}
}