.pagination {
	margin: 0;
	> li {
		&.disabled {
			> a {
				color: @pagination-border;
				&:hover,
				&:focus {
					background-color: @pagination-hover-bg;
					color: @pagination-border;
					outline: 0;
				}
			}
		}
		&.active {
			> a {
				color: @link-selected-color;
				background: transparent;
				border-color: @pagination-border;  
				&:hover {
					color:  @link-selected-color; 
					background: @pagination-hover-bg;
					border-color: @pagination-border;
				} 
				&:focus {
					background: @pagination-hover-bg;
					color: @link-selected-color;
					border-color: @pagination-border;
				}
			}
		}
		> a {
			padding: 4px 12px 3px;
			margin-bottom: 5px;
			span {
				&[class*="ss-"] {
					font-size: 13px;
					line-height: 18px;
					position: relative;
					top: 1px;
				}
				&[class*="fa fa-"] {
					font-size: 13px;
					line-height: 18px;
					position: relative;
					top: 1px;
				}
			}
			&:hover {
				color: @link-color;  
			}
			&:focus {
				outline: 0;
			}
			&:active {
				color: @link-active-color;
				background-color: @pagination-hover-bg;
			}
			&.hoved {
				background-color: @pagination-hover-bg;
			}
			&[aria-label="Previous"],
			&[aria-label="Next"] {
				span {
					&.sr-only {
						position: relative;
						+ [class*="ss-"] {
							font-size: 9px;
							top: 4px; 
						}
						+ [class*="fa fa-"] {
							font-size: 9px;
							top: 3px; 
						}
					}
				}
			}
			&[aria-label="Previous"] {
				span {
					&.sr-only {
						+ [class*="ss-"] {
							float: left;
							margin-right: 5px;
						}
						+ [class*="fa fa-"] {
							float: left;
							margin-right: 5px;
						}
					}
				}
			}
			&[aria-label="Next"] {
				span {
					&.sr-only {
						+ [class*="ss-"] {
							float: right;
							margin-left: 5px;
						}
						+ [class*="fa fa-"] {
							float: right;
							margin-left: 5px;
						}
					}
				}
			}
		}
	}
}

