.m-carousel {
  position: relative;
	overflow: hidden;
	font-size: @font-size-xs;
	line-height: @line-height-xs-computed;
	.m-carousel-inner {
		position: relative;
		font-size: 0;
		text-align: left;
		white-space: nowrap;
		.transition(transform 0.5s);
		> * {
		  display: inline-block;
		  font-size: @font-size-xs;
			line-height: @line-height-xs-computed;
		  white-space: normal;
		  vertical-align: top;
		}
		.m-item {
			text-align: center;
			> a {
				display: block;
				overflow: hidden;
				> img {
					max-width: 100%;
				}
			}
			> p {
				margin: 0;
			}
			a {
				p {
					margin: 6px 0;
				}
			}
		}
	}
	.m-carousel-controls {
		padding-top: 15px;
  	text-align: center;
  	> a {
  		&:hover,
			&:focus {
				text-decoration: none;
				outline: 0;
			}
  	}
		&.m-carousel-pagination {
			> a {
				padding: 4px;
				border-radius: 50%;
				border: 1px solid #A3A3A3;
				background: @white-base;
				display: inline-block;
				margin: 0 10px;
				
				&.m-active {
					background: #A3A3A3;
					cursor: default;
				}
				
				&.m-pager {
					display: none;
					border-radius: 0;
					border: none;
					padding: 0;
					margin: 0;
					color: @text-color;
				}
			}
			&.dark {
				> a {

					&.m-active {
						border-color: @white-base;
						background: #a3a3a3;
					}
				}
			}
		}
		&.m-carousel-arrows {
			padding-top: 0;
			> a {
				position: absolute;
				color: #666;
				top: 50%;
				margin-top: -60px;
				font-size: 22px;
				line-height: 22px;
				padding: 30px 15px;
				&[data-slider="prev"] { 
					left: -15px;
				}
				&[data-slider="next"] {
					right: -15px;
				}
				&:hover {
					color: #999;
				}
				&:active {
					color: @text-color;
				}
				&.m-disabled {
					color: #ddd;
					.opacity(.4);
					cursor: default;
				}
			}
			&.dark {
				> a {
					color: #ccc;
					&:hover {
						color: #999;
					}
					&:active {
						color: #333;
					}
					&.m-disabled {
						color: #d6d6d6;
					}
				}
			}
		}
		&.m-carousel-view-all {
			padding-top: 8px;
			> a {
				&:hover {
					text-decoration: underline;
				}
				> span {
					font-size: 8px;
					line-height: 8px;
				}
			}
		}
	}
}
.pl-carousel {	
	.box-shadow (0 2px 6px rgba(0, 0, 0, 0.2));	 
	padding: 20px 15px 10px;
	&.single-slideshow,
	&.m-single {
		.box-shadow(none);
		padding: 0;
	}
	&.m-single {
		position: relative;
		.m-boxShadow {
			position: absolute;
			content: "";
			width: 100%;
			display: block;
			top: 0;
			box-shadow: 0 2px 6px rgba(0, 0, 0, 0.2);
			left: 0;
			z-index: -1;
			.transition(height .5s);
		}
	}
	.m-carousel {
		&.m-carousel-horizontal {
			.m-carousel-inner {
				.m-item {
				  width: 33.33%;
					padding: 0 12px;
				}
			}
			.m-carousel-controls {
				&.m-carousel-pagination {
					> a {
						 &.m-pager {
							display: none;
						}
					}
				}
			}
		}
		&.m-carousel-single {
			.m-carousel-inner {
				-webkit-transition: transform 0.5s, height 0.5s;
				-moz-transition: transform 0.5s, height 0.5s;
				transition: transform 0.5s, height 0.5s; 
				.m-item {
				  	width: 100%;	
				  	padding: 0;
					> a {
						 padding: 20px;
						 > p {
						 	margin-bottom: 0;
						 }
					}
				}
			}
			.m-carousel-controls {
				&.m-carousel-pagination {
					> a {
						display: none;
						&.m-pager {
							display: inline-block;
						}
					}
				}
				&.m-carousel-arrows {
					> a {
						top: auto;
						bottom: 17px;
						margin-top: 0;
						padding: 15px 15px 8px;
						&[data-slider="prev"] {
							left: 25%;
						}
						&[data-slider="next"] {
							right: 25%;
						}
					}
				}
				&.m-carousel-view-all {
					height: 30px;
				}
			}
		}
		
		&.m-carousel-column {
			.m-carousel-inner {
				.m-item {
				  width: 33.33%;	
				  padding: 0 12px;
					> ul {
						width: 100%;
						margin: 0;
						padding: 0;
						> li {
							list-style: none;
							margin-bottom: 15px;
							> a {
								display: block;
								> img {
									max-width: 100%;
								}
							}
						}
					}
				}
			}
			.m-carousel-controls {
				padding-top: 0;
				&.m-carousel-pagination {
					> a {

					}
				}
				&.m-carousel-arrows {
					> a {

						&[data-slider="prev"] {

						}
						&[data-slider="next"] {

						}
					}
				}
			}
		}
		
		&.m-carousel-vertical {
			padding-top: 20px;
			margin-bottom: 10px;
			.m-carousel-inner {
				.m-item {
				  	width: 100%;
					padding: 12px 0;
					display: block;
	
					> a {
						
					}
				}
			}
			.m-carousel-controls {
				&.m-carousel-pagination {
					position: absolute;
					top: 0;
					right: 0;
					padding-top: 0;
					
					> a {
						display: block;
						margin: 10px 0; 
						
						&.m-pager {
							display: none;
						}
					}
				}
				&.m-carousel-arrows {
					> a {
						margin-top: 0;
						&[data-slider="prev"] {
							left: 50%;
							top: -30px;
							margin-left: -26px;
						}
						&[data-slider="next"] {
							right: 50%;
							top: auto;
							margin-right: -26px;
							&[class^="ss-"] {
								bottom: -38px;
							}
							&[class^="fa fa-"] {
								bottom: -30px;
							}
							
						}
					}
				}
				&.m-carousel-view-all {
					> a {
						position: absolute;
						top: 0;
						right: 0;
					}
				}
			}
		}
		&.m-carousel-slide {
			&:hover {
				.m-carousel-controls {
					&.m-carousel-arrows {
						.opacity(1);
					}
				}
			}
			.m-carousel-inner {
				.m-item {
					width: 100%;	
					padding: 0;
				}
			}
			.m-carousel-controls {
				&.m-carousel-pagination {
					position: relative;
					padding-top: 0;
					top: -30px;
					> a {
						position: relative;
					}
				}
				&.m-carousel-arrows {
					.opacity(0);
					.transition(opacity .3s);
					> a {
						&[data-slider="prev"] {
							left: 0;
						}
						&[data-slider="next"] {
							right: 0; 
						}
					}
				}
			}
		}
		&.m-carousel-thumbnail {
			.m-carousel-inner {
				.m-item {
					width: 100%;	
					padding: 0;
				}
			}
			.m-carousel-controls {
				&.m-carousel-pagination {
					display: none;
				}
				&.m-carousel-thumbs {
					text-align: center;
					padding-top: 0;					
					position: relative; 
					top: -54px;
					> .thumbnails {
						display: inline-block;			
						position: relative;
						margin: 0 auto; 
						padding: 0 1px;
						background: @white-base;
						> li {
							float: left;
							padding: 2px 1px;
							list-style: none;
							> a {
								display: block;
								position: relative;
								cursor: pointer;
								&:hover {
									&:before {
										opacity: 0;
										.transition(all .3s ease);
									}
								}
								&.m-active {
									cursor: default;
									&:before {
										display: none;
									}
								}
								&:before {
									content: "";
									position: absolute;
									top: 0;
									left: 0;
									width: 100%;
									height: 100%;
									background: #000;
									opacity: 0.7;
									display: block;
									.transition(opacity .3s ease);
								}
							}
						}
					}	
				}
				&.m-carousel-arrows {
					> a {
						margin-top: -80px;
						&[data-slider="prev"] {
							left: 0;
						}
						&[data-slider="next"] {
							right: 0;

						}
					}
				}
			}
		}
	}
}

