// Standard search field

.sf-container {
	position: relative;
	border: 1px solid #333;
	border-radius: 3px;
	padding-right: (@width-btn-search-field-md - 1);
}

.sf-btn-submit  {
	position: absolute;
	right: -1px;
	top: -1px;
	border-radius: 0 3px 3px 0;
	width: @width-btn-search-field-md;
	height: (@input-height-base + 2);
	font-size: 20px;
	line-height: 21px;
}
.sf-btn-clear {
	color: #999;
	width: (@font-size-btn-search-clear-md + 10);
	height: (@input-height-base + 2);
	line-height: (@input-height-base + 4);
	font-size: @font-size-btn-search-clear-md;
	position: absolute;
	right: (@width-btn-search-field-md + 10);
	top: -1px;
	text-align: right;
	&:hover,
	&:focus {
		text-decoration: none;
		color: #666;
	}
	&:active {
		color: #333;
	}
}
.sf-input-container {
	overflow: hidden;
}
.sf-input-search {
	border: none;
	.box-shadow(none);
	padding-right: (@font-size-btn-search-clear-md + 20);
}

.sf-xs {
	.sf-container {
		padding-right: (@width-btn-search-field-xs - 1);
	}
	.sf-btn-submit {
		height: (@input-height-xs + 2);
		padding: @input-padding-xs-vertical @input-padding-xs-horizontal (@input-padding-xs-vertical - 1);
		width: @width-btn-search-field-xs;
		font-size: 16px;
		line-height: 18px;
	}
	.sf-btn-clear {
		font-size: @font-size-btn-search-clear-xs;
		height: (@input-height-xs + 2);
		line-height: (@input-height-xs + 4);
		right: (@width-btn-search-field-xs + 10);
	}
	.sf-input-search {
		padding-right: (@font-size-btn-search-clear-xs + 20);
	}
}
.sf-sm {
	.sf-container {
		padding-right: (@width-btn-search-field-sm - 1);
	}
	.sf-btn-submit {
		height: (@input-height-small + 2);
		padding: @padding-small-vertical @input-padding-small-horizontal (@padding-small-vertical - 3);
		width: @width-btn-search-field-sm;
		font-size: 18px;
		line-height: 18px;
	}
	.sf-btn-clear {
		font-size: @font-size-btn-search-clear-sm;
		height: (@input-height-small + 2);
		line-height: (@input-height-small + 4);
		right: (@width-btn-search-field-sm + 10);
	}
	.sf-input-search {
		padding-right: (@font-size-btn-search-clear-sm + 20);
	}
}
.sf-lg {
	.sf-container {
		padding-right: (@width-btn-search-field-lg - 1);
	}
	.sf-btn-submit {
		height: (@input-height-large + 2);
		padding: @padding-large-vertical @input-padding-large-horizontal (@padding-large-vertical - 3);
		width: @width-btn-search-field-lg;		
		font-size: 22px;
		line-height: 24px;
	}
	.sf-btn-clear {
		font-size: @font-size-btn-search-clear-lg;
		height: (@input-height-large + 2);
		line-height: (@input-height-large + 4);
		right: (@width-btn-search-field-lg + 10);
	}
	.sf-input-search {
		padding-right: (@font-size-btn-search-clear-lg + 20);
	}
}

