.pager {
	> li {
		float: left;
		> a {
			margin-left: -1px;
			padding: 4px 12px 3px;
			> span {
				vertical-align: middle;
				&:first-child {
					font-size: 13px;
					line-height: 18px;
					position: relative;
					top: 1px;
				}
			}
			&.hoved {
				background-color: @pager-hover-bg;
			}
		}
		&.disabled {
			> a {
				color: @pager-border;  
				&:hover,
				&:focus {
					background-color: @pager-hover-bg;
					color: @pager-border;
					outline: 0;
				}
			}
		}
		&.previous {
			> a {
				border-radius: 3px 0 0 3px;
			}
		}
		&.next {
			> a {
				border-radius: 0 3px 3px 0;
			}
		}
	}
}


