.calendar-picker {
	input {
		&.datepicker {
			width: 220px;
		}
	}
	img {
		&.ui-datepicker-trigger {
			width: 32px;
			height: 32px;
			vertical-align: top; 
			margin-top: 2px;
			cursor: pointer;
		}
	}
	&.cp-lg {
		img {
			&.ui-datepicker-trigger {
				width: 38px;
				height: 38px;
			}
		}
	}
	&.cp-sm {
		img {
			&.ui-datepicker-trigger {
				width: 26px;
				height: 26px;
			}
		}
	}
	&.cp-xs {
		img {
			&.ui-datepicker-trigger {
				width: 20px;
				height: 20px;
			}
		}
	}
	.cp-range {
		display: inline-block;
		position: relative;
		input {
			width: 60px; 
			display: inline-block;
			margin: 0 6px;
			text-align: center;
			&.cp-year {
				width: 75px;
			}
			&:first-child {
				margin-left: 0;
			}

			&.datepicker {
				position: absolute;
				visibility: hidden;
				left: 0;
				width: 100%;
				margin-left: 0;
			}
		}
	}
}

.datepicker {
	.ui-datepicker {
		width: 100%;
	}
}

.ui-datepicker {
	width: 220px;
	padding-top: .4em;
	border: none;
	background: #f8f8f8;
	.box-shadow(0 1px 3px rgba(0,0,0,.25));
	.ui-datepicker-header {
		border: none;
		background: transparent;
		color: @text-color;
		font-size: 12px;
		.ui-datepicker-prev,
		.ui-datepicker-next {
			cursor: pointer;
			.ui-icon {
				font-size: 13px;
				width: 13px;
			}
			> span {
				background: none;
				text-indent: 0;
				color: @link-color;
				height: 20px;
			}
			&.ui-state-disabled {
				cursor: default;
				> span {
					color: #ddd;
				}
			}
			&.ui-datepicker-prev-hover,
			&.ui-datepicker-next-hover {
				background: transparent;
				border: none;
				top: 2px;
			} 
			&.ui-datepicker-next-hover {
				right: 2px;
			}
			&.ui-datepicker-prev-hover {
				left: 2px;
			}
		}
	}
	
	.ui-datepicker-calendar {
		th {
			padding: 2px;
			> span {
				font-size: 10px;
				line-height: 12px;
				font-weight: normal;
				color: #999;
				text-transform: uppercase;
			}
		}
		td {
			> a,
			> span {
				padding: 7px;
				font-size: 11px;
				line-height: 14px;
				text-align: center;
			}
			> a {
				z-index: 2;
				&.ui-state-default {
					position: relative;
					color: @text-color;
					&:before {
						content: "";
						position: absolute;
						display: block;
						padding: 12px;
						border-radius: 100%;
						z-index: -1;
						margin-top: -12px;
						margin-left: -12px;
						top: 50%;
						left: 50%;
					}
				}
				&.ui-state-default,
				&.ui-state-hover,
				&.ui-state-focus {
					border: none;
					background: transparent;
				}
				&.ui-state-hover,
				&.ui-state-active {
					color: @white-base;
				}
				&:active,
				&.ui-state-focus {
					color: @white-base;
					&:before {
						background: @link-active-color;
					}
				}
				&.ui-state-hover {
					&:before {
						background: @link-color;
					}
				}
				&.ui-state-active {
					&:before {
						background: @link-selected-color;
					}
				}
				
			}
			&.ui-state-disabled {
				> span {
					&.ui-state-default {
						border: none;
						background: transparent;
						color: #bbb;
						opacity: 1;
					}
				}
			}
			&.ui-datepicker-today {
				> a {
					&.ui-state-highlight,
					&.ui-state-hover,
					&.ui-state-focus,
					&.ui-state-active,
					&:active {
						color: @text-color;
						&:before {
							background: #e6e6e6;
						}
					}
				}
			}
		}
	}
}
