//
// Dropdown menus
// --------------------------------------------------




// The dropdown menu (ul)
.dropdown-menu {
	border: 0;
  .box-shadow(0 1px 5px rgba(0,0,0,.15));
}

// Hover/Focus/Active state
.dropdown-menu > li > a {
  &:hover,
  &:focus {
    color: @dropdown-link-color;
  }
	&:active {
		color: @dropdown-link-active-color;
    background-color: @dropdown-link-active-bg;
	}
}

// Selected state
.dropdown-menu > .selected > a {
  &,
  &:hover,
  &:focus {
    color: @dropdown-link-selected-color;
    text-decoration: none;
    outline: 0;
    background-color: @dropdown-link-selected-bg;
  }
}

// Custom Drops
//
.dropdown-toggle {	
	.ss-icon {
		font-size: .85em;
	}
}

a {
	&.dropdown-toggle {	
		.ss-icon {
			font-size: .65em;
		}
	}
}
.dropdown-toggle {	
	[class^="fa fa-"] {
		font-size: .95em;
	}
}

a {
	&.dropdown-toggle {	
		[class^="fa fa-"] {
			font-size: .75em;
		}
	}
}

button {
	+ button {
		&.dropdown-toggle {
			.ss-icon {
				font-size: 14px;
			}	
			[class^="fa fa-"] {
				font-size: 14px;
			}
		}
	}
}

.dropdown-toggle {
	[class^="fa fa-"] {
		position: relative;
		top: -2px;
	}
}
