// Button variants
//
// Easily pump out default styles, as well as :hover, :focus, :active,
// and disabled options for all buttons

.button-variant-pl(@color-normal; @color-active; @bg-normal; @bg-active; @border-normal; @border-active) {
  color: @color-normal;
  background-color: @bg-normal;
  border-color: @border-normal;
	&:hover,
	&:focus,
	&.focus {
		color: @color-normal;
		background-color: @border-normal;
		border-color: @border-normal;
	}
	&.active,
	&:active {
		color: @color-active;
		background-color: @bg-active;
		border-color: @border-active;
	}
  &.disabled,
  &[disabled],
  fieldset[disabled] & {
    &,
    &:hover,
    &:focus,
    &.focus,
    &:active,
    &.active {
      background-color: @btn-disabled-bg;
          border-color: @btn-disabled-border;
								 color: @btn-disabled-color;
    }
  }
}
// Button sizes
.button-size-pl(@padding-top; @padding-bottom; @padding-horizontal; @font-size; @line-height) {
  padding: @padding-top @padding-horizontal @padding-bottom;
  font-size: @font-size;
  line-height: @line-height;
}